export const ELEMENT_KEY_TO_NAV_NODE = 'ELEMENT_KEY_TO_NAV_NODE';
export const CURRENT_NAV_TREE = 'CURRENT_NAV_TREE';

const inMemoryStorage: any = {};

export const getMemoryStorageItem = (key: string): any | null => {
  return inMemoryStorage[key];
};

export const setMemoryStorageItem = (key: string, value: any | null): void => {
  inMemoryStorage[key] = value;

  if (!value) {
    delete inMemoryStorage[key];
  }
};

export const removeMemoryStorageItem = (key: string): void => {
  delete inMemoryStorage[key];
};

export const getSessionStorageItem = (
  key: string,
  storyboardId?: string,
): string | null => {
  return sessionStorage.getItem(`${storyboardId}_${key}`);
};

export const setSessionStorageItem = (
  key: string,
  value: string | null,
  storyboardId?: string,
): void => {
  if (!value) {
    removeSessionStorageItem(key, storyboardId);
    return;
  }

  sessionStorage.setItem(`${storyboardId}_${key}`, value);
};

export const removeSessionStorageItem = (
  key: string,
  storyboardId?: string,
): void => {
  sessionStorage.removeItem(`${storyboardId}_${key}`);
};
