import { TempoElement } from './tempoElement';
import { v4 as uuidv4 } from 'uuid';

// Matches the file in tempo-devtools
export enum StylingFramework {
  INLINE = 'Inline',
  CSS = 'CSS',
  TAILWIND = 'Tailwind',
}

type ClassType = {
  tailwind: string;
  css: string;
};

export enum ChangeType {
  STYLING = 'STYLING',
  ADD_JSX = 'ADD_JSX',
  MOVE_JSX = 'MOVE_JSX',
  REMOVE_JSX = 'REMOVE_JSX',
  CHANGE_PROP = 'CHANGE_PROP',
  ADD_CLASS = 'ADD_CLASS',
  REMOVE_CLASS = 'REMOVE_CLASS',
  EDIT_TEXT = 'EDIT_TEXT',
  WRAP_DIV = 'WRAP_DIV',
  CHANGE_TAG = 'CHANGE_TAG',
  DUPLICATE = 'DUPLICATE',
  UNDO = 'UNDO',
  REDO = 'REDO',
  UNKNOWN = 'UNKNOWN',
}

// Make sure to match this in both tempo-devtools & ** tempo-api ** (in the undo/redo file)
export const CHANGE_TYPES_WITH_INSTANT_UNDO = [
  ChangeType.REMOVE_JSX,
  ChangeType.ADD_CLASS,
  ChangeType.STYLING,
];

export abstract class ChangeLedgerItem<T> {
  public id: string;
  public type: ChangeType;
  public changeName: string;
  public indexInLedger?: number;
  public changeFields: T;

  public canInstantUpdateWhileFlushing: boolean;

  // These two fields are set after the API request completes
  public activityId?: string;
  public prevIdToNewIdMap: { [prevId: string]: string } = {};

  public changeUndone?: boolean;
  public toastId?: string | number;

  private _consumed: boolean;
  private _failed: boolean;

  private _instantUpdateSent: boolean;
  private _instantUpdateFinished: boolean;
  private _instantUpdateSuccessful: boolean;
  private _sendInstantUpdate: boolean;

  // Extra data given after the instant update (e.g. the inner HTML for deleted elements)
  private _instantUpdateData?: any;

  // After instant update, which selected element key & multiselected element keys to set
  // Note -> we are not using callbacks for the undo instant update because we store this in
  // local storage then deserialize it, so the callbacks are lost
  // Also Note -> these element keys are selected explicitly in tempo-devtools
  // where the instant updates are performed (change item functions)

  // For instant update
  // Null will be set, while undefined will be ignored
  private elementKeyToSelectAfterInstantUpdate?: string | null;
  private elementKeysToMultiselectAfterInstantUpdate?: string[] | null;

  // For undo instant update
  // Null will be set, while undefined will be ignored
  private elementKeyToSelectAfterUndoInstantUpdate?: string | null;
  private elementKeysToMultiselectAfterUndoInstantUpdate?: string[] | null;

  // Resolvers after API call

  private _resolveApi?: (data?: any) => void;
  private _rejectApi?: (reason?: any) => void;
  private _apiRejectionAdded?: boolean;

  private _apiPromise: Promise<void>;

  constructor(
    type: ChangeType,
    changeName: string,
    changeFields: T,
    id?: string,
  ) {
    this.id = id || uuidv4();
    this.type = type;
    this.changeFields = changeFields;
    this.changeName = changeName;
    this._consumed = false;
    this._failed = false;
    this._instantUpdateSent = false;
    this._instantUpdateFinished = false;
    this._instantUpdateSuccessful = false;
    this._sendInstantUpdate = true;
    this.canInstantUpdateWhileFlushing = false;

    this._apiPromise = new Promise<void>((resolve, reject) => {
      this._resolveApi = resolve;
      this._rejectApi = reject;
    });
  }

  public resolveApi(data?: any) {
    this._resolveApi?.(data);
  }

  public rejectApi(reason?: any) {
    if (this._apiRejectionAdded) {
      this._rejectApi?.(reason);
    }
  }

  public needsToSendInstantUpdate() {
    return !this._instantUpdateSent && this._sendInstantUpdate;
  }

  public markInstantUpdateSent() {
    this._instantUpdateSent = true;
  }

  public markInstantUpdateFinished(
    instantUpdateData: any,
    instantUpdateSuccessful: boolean,
  ) {
    this._instantUpdateFinished = true;
    this._instantUpdateSuccessful = instantUpdateSuccessful;
    this._instantUpdateData = instantUpdateData;
  }

  public getInstantUpdateData() {
    return this._instantUpdateData;
  }

  public wasInstantUpdateSuccessful() {
    return this._instantUpdateSuccessful;
  }

  public isInstantUpdateFinished() {
    return this._instantUpdateFinished;
  }

  public markProcessedSucceeded() {
    this._consumed = true;
  }

  public markProcessedFailed() {
    this._failed = true;
    this._consumed = true;
  }

  public isFailed() {
    return this._failed;
  }

  public needToProcessChange() {
    return !this._consumed;
  }

  public onApiResolve(onFulfilled: (data?: any) => void) {
    return this._apiPromise.then(onFulfilled);
  }

  public onApiReject(onRejected: (reason?: any) => void) {
    this._apiRejectionAdded = true;
    return this._apiPromise.catch(onRejected);
  }

  abstract prepareApiRequest(
    canvasId: string,
    treeElementLookup: { [codebaseId: string]: any },
    activeCanvas: any,
  ): {
    urlPath: string;
    body: {
      [key: string]: any;
    };
    successToastMessage?: string;
  };

  public doNotSendInstantUpdate() {
    this._sendInstantUpdate = false;
  }

  // For selecting/deslecting new elements after instant updates

  public clearSelectedElementsAfterInstantUpdate() {
    this.elementKeyToSelectAfterInstantUpdate = null;
    this.elementKeysToMultiselectAfterInstantUpdate = null;
  }

  public setSelectedElementsAfterInstantUpdate(
    selectedElementKey: string,
    multiselectedElementKeys: string[],
  ) {
    this.elementKeyToSelectAfterInstantUpdate = selectedElementKey;
    this.elementKeysToMultiselectAfterInstantUpdate = multiselectedElementKeys;
  }

  public clearSelectedElementsAfterUndoInstantUpdate() {
    this.elementKeyToSelectAfterUndoInstantUpdate = null;
    this.elementKeysToMultiselectAfterUndoInstantUpdate = null;
  }

  public setSelectedElementsAfterUndoInstantUpdate(
    selectedElementKey: string,
    multiselectedElementKeys: string[],
  ) {
    this.elementKeyToSelectAfterUndoInstantUpdate = selectedElementKey;
    this.elementKeysToMultiselectAfterUndoInstantUpdate =
      multiselectedElementKeys;
  }

  public getElementKeyToSelectAfterInstantUpdate() {
    return this.elementKeyToSelectAfterInstantUpdate;
  }

  public getElementKeysToMultiselectAfterInstantUpdate() {
    return this.elementKeysToMultiselectAfterInstantUpdate;
  }

  public getElementKeyToSelectAfterUndoInstantUpdate() {
    return this.elementKeyToSelectAfterUndoInstantUpdate;
  }

  public getElementKeysToMultiselectAfterUndoInstantUpdate() {
    return this.elementKeysToMultiselectAfterUndoInstantUpdate;
  }

  abstract applyCodebaseIdChanges(prevIdToNewIdMap: {
    [prevId: string]: string;
  }): void;

  public applyAllCodebaseIdChanges(prevIdToNewIdMap: {
    [prevId: string]: string;
  }) {
    const getNewKey = (prevKey: string) => {
      if (!prevKey) {
        return null;
      }

      const tempoElement = TempoElement.fromKey(prevKey);
      const codebaseId = tempoElement.codebaseId;
      const newCodebaseId = prevIdToNewIdMap[codebaseId];

      if (newCodebaseId) {
        return new TempoElement(
          newCodebaseId,
          tempoElement.storyboardId,
          tempoElement.uniquePath,
        ).getKey();
      }

      return null;
    };

    /*
     * Instant update fields
     */

    if (this.elementKeyToSelectAfterInstantUpdate) {
      const newElementKey = getNewKey(
        this.elementKeyToSelectAfterInstantUpdate,
      );
      this.elementKeyToSelectAfterInstantUpdate =
        newElementKey || this.elementKeyToSelectAfterInstantUpdate;
    }

    if (this.elementKeysToMultiselectAfterInstantUpdate) {
      this.elementKeysToMultiselectAfterInstantUpdate =
        this.elementKeysToMultiselectAfterInstantUpdate?.map((key) => {
          const newKey = getNewKey(key);
          return newKey || key;
        });
    }

    /*
     * Undo instant update fields
     */
    if (this.elementKeyToSelectAfterUndoInstantUpdate) {
      const newElementKey = getNewKey(
        this.elementKeyToSelectAfterUndoInstantUpdate,
      );
      this.elementKeyToSelectAfterUndoInstantUpdate =
        newElementKey || this.elementKeyToSelectAfterUndoInstantUpdate;
    }

    if (this.elementKeysToMultiselectAfterUndoInstantUpdate) {
      this.elementKeysToMultiselectAfterUndoInstantUpdate =
        this.elementKeysToMultiselectAfterUndoInstantUpdate?.map((key) => {
          const newKey = getNewKey(key);
          return newKey || key;
        });
    }

    this.applyCodebaseIdChanges(prevIdToNewIdMap);
  }
}

/************************************************************************
 * Styling Change
 ************************************************************************/

export interface StylingChangeFields {
  codebaseId: string;
  stylingChanges: { [cssKey: string]: string };

  // Currently only one supported
  stylingFramework: StylingFramework.TAILWIND;
  modifiers: string[];
  customProperties: ClassType[];
}

export class StylingChange extends ChangeLedgerItem<StylingChangeFields> {
  constructor(changeFields: StylingChangeFields, id?: string) {
    super(ChangeType.STYLING, 'Styling', changeFields, id);

    // Allow instant updates while flushing
    this.canInstantUpdateWhileFlushing = true;
  }

  public prepareApiRequest(
    canvasId: string,
    treeElementLookup: { [codebaseId: string]: any },
    activeCanvas: any,
  ) {
    const {
      codebaseId,
      stylingChanges,
      stylingFramework,
      modifiers,
      customProperties,
    } = this.changeFields;

    return {
      urlPath: `canvases/${canvasId}/parseAndMutate/mutate/styling`,
      body: {
        reactElement: treeElementLookup[codebaseId],
        styling: stylingChanges,
        stylingFramework,
        modifiers,
        customProperties,
      },
    };
  }

  public applyCodebaseIdChanges(prevIdToNewIdMap: {
    [prevId: string]: string;
  }) {
    const newCodebaseId = prevIdToNewIdMap[this.changeFields.codebaseId];

    if (newCodebaseId) {
      this.changeFields.codebaseId = newCodebaseId;
    }
  }
}

/************************************************************************
 * Add Jsx Change
 ************************************************************************/

export interface AddJsxChangeFields {
  codebaseIdToAddTo: string;
  beforeCodebaseId?: string;
  afterCodebaseId?: string;

  // The element to add
  addCodebaseId?: string;
  addNativeTag?: string;
  fileContentsToSourceFrom?: string;
  fileContentsSourceFilename?: string;

  propsToSet?: { [key: string]: any };

  // Used to track the storyboard in activity stream
  deletedStoryboardId?: string;

  // For instant update
  htmlForInstantUpdate?: string;
}

export class AddJsxChange extends ChangeLedgerItem<AddJsxChangeFields> {
  constructor(changeFields: AddJsxChangeFields, id?: string) {
    super(ChangeType.ADD_JSX, 'Add Element', changeFields, id);
  }

  public prepareApiRequest(
    canvasId: string,
    treeElementLookup: { [codebaseId: string]: any },
    activeCanvas: any,
  ) {
    const {
      codebaseIdToAddTo,
      beforeCodebaseId,
      afterCodebaseId,
      addCodebaseId,
      addNativeTag,
      fileContentsToSourceFrom,
      fileContentsSourceFilename,
      propsToSet,
      deletedStoryboardId,
      htmlForInstantUpdate,
    } = this.changeFields;

    const body: any = {
      destinationElement: treeElementLookup[codebaseIdToAddTo],
      beforeElement: treeElementLookup[beforeCodebaseId || ''],
      afterElement: treeElementLookup[afterCodebaseId || ''],
      newElement: {},
      canvasId: activeCanvas.id,
      deletedStoryboardId,
      fileContentsToSourceFrom,
      fileContentsSourceFilename,
    };

    if (addCodebaseId) {
      body.newElement = {
        ...treeElementLookup[addCodebaseId],
      };
    } else if (addNativeTag) {
      body.newElement['type'] = 'native';
      body.newElement['nativeTag'] = addNativeTag;
      body.newElement['componentName'] = addNativeTag;
    }

    if (propsToSet) {
      body.newElement['propsToSet'] = propsToSet;
    }

    if (!Object.keys(body.newElement).length) {
      delete body.newElement;
    }

    const hasInstantUpdate = Boolean(htmlForInstantUpdate);
    body['hasInstantUpdate'] = hasInstantUpdate;

    return {
      urlPath: `canvases/${canvasId}/parseAndMutate/mutate/addJsxElement`,
      body,

      // Only show the success message if we do not have instant updates
      successToastMessage: hasInstantUpdate ? undefined : 'Successfully added',
    };
  }

  public applyCodebaseIdChanges(prevIdToNewIdMap: {
    [prevId: string]: string;
  }) {
    const fieldsToApply = [
      'codebaseIdToAddTo',
      'beforeCodebaseId',
      'afterCodebaseId',
      'addCodebaseId',
    ];

    fieldsToApply.forEach((field) => {
      // @ts-ignore
      const newCodebaseId = prevIdToNewIdMap[this.changeFields[field]];

      if (newCodebaseId) {
        // @ts-ignore
        this.changeFields[field] = newCodebaseId;
      }
    });
  }
}

