/************************************************************************
 * Remove JSX Change
 ************************************************************************/

export interface RemoveJsxChangeFields {
  codebaseIdsToRemove: string[];
}

export class RemoveJsxChange extends ChangeLedgerItem<RemoveJsxChangeFields> {
  constructor(changeFields: RemoveJsxChangeFields, id?: string) {
    // Deduplicate the codebaseIdsToRemove
    changeFields.codebaseIdsToRemove = Array.from(
      new Set(changeFields.codebaseIdsToRemove),
    );
    super(ChangeType.REMOVE_JSX, 'Delete Element', changeFields, id);
  }

  public prepareApiRequest(
    canvasId: string,
    treeElementLookup: { [codebaseId: string]: any },
    activeCanvas: any,
  ) {
    const { codebaseIdsToRemove } = this.changeFields;

    return {
      urlPath: `canvases/${canvasId}/parseAndMutate/mutate/removeJsxElement`,
      body: {
        elementsToRemove: codebaseIdsToRemove
          .map((codebaseId: string) => treeElementLookup[codebaseId])
          .filter((element: any) => element),
      },
    };
  }

  public applyCodebaseIdChanges(prevIdToNewIdMap: {
    [prevId: string]: string;
  }) {
    this.changeFields.codebaseIdsToRemove =
      this.changeFields.codebaseIdsToRemove.map((codebaseId: string) => {
        const newCodebaseId = prevIdToNewIdMap[codebaseId];

        if (newCodebaseId) {
          return newCodebaseId;
        }

        return codebaseId;
      });
  }
}

