// Code taken and adapted from the resq library: https://github.com/baruchvlz/resq

import {
  addUniqueLookupAsClass,
  getUniqueLookupFromNode,
  removeUniqueLookupFromNode,
} from './identifierUtils';
import { v4 as uuidv4 } from 'uuid';

/**
 * Taken from https://github.com/baruchvlz/resq/blob/master/src/utils.js
 * but improved to work with all versions of react
 */
export const findReactInstance = (element: any): any => {
  if (element.hasOwnProperty('_reactRootContainer')) {
    if (element._reactRootContainer._internalRoot) {
      return element._reactRootContainer._internalRoot.current;
    } else {
      return element._reactRootContainer.current;
    }
  }

  const instanceId = Object.keys(element).find(
    (key) =>
      key.startsWith('__reactInternalInstance') ||
      key.startsWith('__reactFiber') ||
      key.startsWith('__reactContainer'),
  );

  if (instanceId) {
    return element[instanceId];
  }
};

//Returns true if it is a DOM element
function isElement(o: any) {
  return typeof HTMLElement === 'object'
    ? o instanceof HTMLElement //DOM2
    : o &&
        typeof o === 'object' &&
        o !== null &&
        o.nodeType === 1 &&
        typeof o.nodeName === 'string';
}

export const getRootReactElement = (): any => {
  let rootSelector = '#root';
  if (!document.querySelector(rootSelector)) {
    rootSelector = '#__next';
  }

  const root: any = document.querySelector(rootSelector);

  let findInstance: any = null;
  if (root) {
    findInstance = findReactInstance(root);
  } else {
    // hacky fallback; if there's no root element so we grab the first one recursively
    function findInstanceInNode(node: any) {
      if (findInstance) {
        return;
      }

      findInstance = findReactInstance(node);
      if (findInstance) {
        return;
      }

      node.childNodes?.forEach((childNode: any) => {
        findInstanceInNode(childNode);
      });
    }

    findInstanceInNode(document.getElementsByTagName('body')[0]);
  }

  // June 12 2024 fix:
  // Sometimes the react tree only loads correctly in the "alternate slot"
  // Replace the current tree with the alternate tree if that is the case
  if (findInstance && !findInstance.child && findInstance.alternate?.child) {
    findInstance = findInstance.alternate;
  }

  return findInstance;
};

const removeChildrenFromProps = (props: any) => {
  // if the props is a string, we can assume that it's just the text inside a html element
  if (!props || typeof props === 'string') {
    return props;
  }

  const returnProps = { ...props };

  delete returnProps.children;

  return returnProps;
};

const getElementState = (elementState: any) => {
  if (!elementState) {
    return undefined;
  }

  const { baseState } = elementState;

  if (baseState) {
    return baseState;
  }

  return elementState;
};

export const buildNodeTree = (element: any, parentTreeNode: any): any => {
  let tree: any = { children: [] };

  tree.element = element;
  tree.parent = parentTreeNode;

  if (!element) {
    return tree;
  }

  tree.name = getElementName(element.type);
  if (typeof tree.name !== 'string') {
    tree.name = tree.name?.toString?.();
  }

  tree.props = removeChildrenFromProps(element.memoizedProps);
  tree.state = getElementState(element.memoizedState);

  let { child } = element;

  if (child) {
    tree.children.push(child);

    while (child.sibling) {
      tree.children.push(child.sibling);
      child = child.sibling;
    }
  }

  tree.children = tree.children.map((child: any) => buildNodeTree(child, tree));

  return tree;
};

export const getDomElementForReactNode = (node: any) => {
  let stateNode = node?.element?.stateNode;
  if (stateNode && stateNode?.constructor?.name === 'FiberRootNode') {
    stateNode = stateNode.containerInfo;
  }

  if (isElement(stateNode)) {
    return stateNode;
  }

  return null;
};

export const getUniqueLoookupFromReactTreeNode = (node: any) => {
  const stateNode = getDomElementForReactNode(node);

  if (stateNode) {
    return getUniqueLookupFromNode(stateNode);
  }

  return null;
};

export const addUniqueLoookupToReactTreeNode = (
  node: any,
  uniqueLookup: string,
) => {
  const stateNode = getDomElementForReactNode(node);

  if (stateNode) {
    addUniqueLookupAsClass(stateNode, uniqueLookup);
    return true;
  }

  return false;
};

export const removeUniqueLoookupFromReactTreeNode = (node: any) => {
  const stateNode = getDomElementForReactNode(node);

  if (stateNode) {
    removeUniqueLookupFromNode(stateNode);
    return true;
  }

  return false;
};

/**
 * Builds a lookup map with generated uuids that are added to the HTML elements under the hood as classes.
 * Make sure to clear the lookups from the tree after you are done with it.
 * @param tree
 * @param map
 */
export const buildTreeLookupMap = (tree: any, map: { [key: string]: any }) => {
  const newUniqueLookup = uuidv4();

  const added = addUniqueLoookupToReactTreeNode(tree, newUniqueLookup);
  if (added) {
    map[newUniqueLookup] = tree;
  }

  tree.children.forEach((child: any) => {
    buildTreeLookupMap(child, map);
  });
};

/**
 * Build tree lookup map adds a class name for lookups, we want to remove these after the tree is built
 * @param tree
 */
export const clearLookupsFromTree = (tree: any) => {
  removeUniqueLoookupFromReactTreeNode(tree);

  tree.children.forEach((child: any) => {
    clearLookupsFromTree(child);
  });
};

const isFunction = (type: any) => {
  return typeof type === 'function';
};

const isObject = (type: any) => {
  return typeof type === 'object';
};

export const getElementName = (type: any) => {
  if (!type) {
    return type;
  }

  if (isFunction(type) || isObject(type)) {
    if (type.displayName) {
      if (isFunction(type.displayName)) {
        return type.displayName();
      } else {
        return type.displayName;
      }
    }

    if (type.name) {
      if (isFunction(type.name)) {
        return type.name();
      } else {
        return type.name;
      }
    }

    if (type.render?.name) {
      return type.render.name;
    }

    return null;
  }

  return type;
};

/**
 * @param tree
 * @param searchFn
 * @param firstOnly if set, returns only the first element in a breadth-firth search
 * @returns
 */
export const findElementInTree = (
  tree: any,
  searchFn: (node: any) => boolean,
  firstOnly?: boolean,
) => {
  let searchQueue = [tree];
  const foundNodes = [];
  while (searchQueue.length > 0) {
    const node = searchQueue.shift();

    if (searchFn(node)) {
      foundNodes.push(node);

      if (firstOnly) {
        break;
      }
    }

    searchQueue = searchQueue.concat(node.children || []);
  }

  return foundNodes;
};
