import { parse } from './cssFunctions';

export const canApplyCssRuleToElement = (cssRule: string, element: any) => {
  try {
    if (!element) {
      return false;
    }

    if (!isCssSelectorValid(cssRule)) {
      return false;
    }

    if (element.matches(cssRule)) {
      return false;
    }

    const parsedCssRule = parse(cssRule);
    let lastRule: any = parsedCssRule;

    while (lastRule.nestedRule) {
      lastRule = lastRule.nestedRule;
    }

    const addedClasses: any = [];
    const classes = new Set(element.classList);

    lastRule.items?.forEach((item: any) => {
      if (item.type === 'ClassName') {
        const cls = item.name;
        if (!classes.has(cls)) {
          element.classList.add(cls);
          addedClasses.push(cls);
        }
      }
    });

    const canApply = element.matches(cssRule);
    addedClasses.forEach((cls: string) => {
      element.classList.remove(cls);
    });

    return canApply;
  } catch (e) {
    console.error(e);
    return false;
  }
};

export const canRemoveCssClassFromElement = (cssRule: string, element: any) => {
  try {
    if (!isCssSelectorValid(cssRule)) {
      return false;
    }

    if (!element.matches(cssRule)) {
      return false;
    }

    const parsedCssRule = parse(cssRule);
    let lastRule: any = parsedCssRule;

    while (lastRule.nestedRule) {
      lastRule = lastRule.nestedRule;
    }

    const removedClasses: any = [];
    const classes = new Set(element.classList);

    lastRule.items?.forEach((item: any) => {
      if (item.type === 'ClassName') {
        const cls = item.name;
        if (!classes.has(cls)) {
          return;
        }

        element.classList.remove(cls);
        removedClasses.push(cls);
      }
    });

    const canRemove = !element.matches(cssRule);
    removedClasses.forEach((cls: string) => {
      element.classList.add(cls);
    });

    return canRemove;
  } catch (e) {
    console.error(e);
    return false;
  }
};
export const getAllClassesFromSelector = (cssSelector: string): Set<string> => {
  try {
    if (!isCssSelectorValid(cssSelector)) {
      return new Set();
    }

    const parsedCssRule = parse(cssSelector);
    let traverseRule: any = parsedCssRule;
    const allClasses = new Set<string>();

    while (traverseRule) {
      const items = traverseRule.items || [];
      items.forEach((item: any) => {
        if (item.type === 'ClassName') {
          allClasses.add(item.name);
        }
      });
      traverseRule = traverseRule.nestedRule;
    }

    return allClasses;
  } catch (e) {
    console.log(
      'Failed to parse classes from selector ' + cssSelector + ', ' + e,
    );
    return new Set();
  }
};

const queryCheck = (s: any) =>
  document.createDocumentFragment().querySelector(s);

export const isCssSelectorValid = (cssSelector: string) => {
  try {
    queryCheck(cssSelector);

    const parsedCssRule = parse(cssSelector);
    return true;
  } catch (e) {
    return false;
  }
};

export const camelToSnakeCase = (str: string) => {
  if (!str) return str;

  return (
    str.charAt(0).toLowerCase() +
    str.substring(1).replace(/[A-Z]/g, (letter) => `-${letter.toLowerCase()}`)
  );
};
