import { ELEMENT_KEY_PREFIX } from './identifierUtils';
import {
  SELECTED_ELEMENT_KEY,
  TEXT_EDIT,
  TREE_ELEMENT_LOOKUP,
  getMemoryStorageItem,
  setMemoryStorageItem,
} from './sessionStorageUtils';
import { TempoElement } from './tempoElement';
import { FIXED_IFRAME_MESSAGE_IDS } from './constantsAndTypes';
import $ from 'jquery';

/**
 * Evaluates if the element's text can be edited in place.
 *
 * @param element
 */
export const canEditText = (element: TempoElement): boolean => {
  const treeElements = getMemoryStorageItem(TREE_ELEMENT_LOOKUP) || {};
  const treeElement = treeElements[element.codebaseId];

  if (!treeElement) {
    return false;
  }

  return treeElement.staticTextContents;
};

/**
 * Returns if the node has text contents in the DOM
 */
export const hasTextContents = (node?: HTMLElement): boolean => {
  if (!node) {
    return false;
  }

  let hasText = false;
  let hasNonText = false;
  node.childNodes.forEach((child) => {
    if (child.nodeType === Node.TEXT_NODE) {
      hasText = true;
      return;
    }

    hasNonText = true;
  });

  return hasText && !hasNonText;
};

/**
 * Evaluates if there's currently an element being edited.
 */

interface EditingInfo {
  key: string;
  originalText: string;
}

export const currentlyEditing = (): boolean => {
  const item = getMemoryStorageItem(TEXT_EDIT);
  return item !== null && item !== undefined;
};

const markAsEditing = (info: EditingInfo): void => {
  setMemoryStorageItem(TEXT_EDIT, info);
};

export const getEditingInfo = (): EditingInfo | null => {
  return getMemoryStorageItem(TEXT_EDIT);
};

const clearEditingInfo = (): void => {
  setMemoryStorageItem(TEXT_EDIT, null);
};

/**
 * Takes an element and registers it as an editable text element.
 * Mutates the DOM to make the element editable.
 */
export const setupEditableText = (
  element: TempoElement,
  parentPort: any,
  storyboardId: string,
) => {
  const classToSearchFor = `.${ELEMENT_KEY_PREFIX}${element.getKey()}`;
  const domElement = $(classToSearchFor).get(0);

  if (!domElement) {
    return;
  }

  const originalText = $(domElement).text();

  markAsEditing({
    key: element.getKey(),
    originalText,
  });
  parentPort.postMessage({
    id: FIXED_IFRAME_MESSAGE_IDS.START_EDITING_TEXT,
    data: {
      key: element.getKey(),
      oldText: originalText,
    },
  });

  $(domElement).attr('contenteditable', 'plaintext-only').trigger('focus');

  // Apply styling directly
  $(domElement).css({
    cursor: 'text',
    outline: 'none',
    border: 'none',
  });

  $(domElement).on('blur', () =>
    teardownEditableText(parentPort, storyboardId),
  );
};

/**
 * Used to mark the completion of the editable text process.
 * Reverts the DOM to its original state.
 * Sends a message to the housing frame with updated text, if necessary.
 *
 */
export const teardownEditableText = (
  parentPort: any,
  storyboardId: string,
): void => {
  const editingInfo = getEditingInfo();

  if (!currentlyEditing()) {
    return;
  }

  clearEditingInfo();

  if (!editingInfo) {
    return;
  }

  const classToSearchFor = `.${ELEMENT_KEY_PREFIX}${editingInfo.key}`;
  const domElement = $(classToSearchFor).get(0);

  if (!domElement) {
    return;
  }

  const updatedText = $(domElement).text();

  parentPort.postMessage({
    id: FIXED_IFRAME_MESSAGE_IDS.EDITED_TEXT,
    data: {
      key: editingInfo.key,
      newText: updatedText,
      oldText: editingInfo.originalText,
    },
  });

  // Clear any selection
  window.getSelection()?.removeAllRanges();

  // Cleanup
  $(domElement).removeAttr('contenteditable').off('blur').css({
    cursor: '',
    outline: '',
    border: '',
  });

  clearEditingInfo();
};
