import { cssEval } from './cssFunctions';
import {
  KNOWN_ATTRIBUTES,
  addElementKeyAsClass,
  getAllUnknownClasesFromList,
  getAllUnknownClasses,
  getCodebaseIdFromNode,
  getUniqueLookupFromNode,
  isSkipNavTreeNode,
} from './identifierUtils';
import { isNodeOutline } from './outlineUtils';
import $ from 'jquery';
import {
  MULTI_SELECTED_ELEMENT_KEYS,
  NAV_TREE_CALLBACKS,
  ORIGINAL_STORYBOARD_URL,
  SAVED_STORYBOARD_COMPONENT_FILENAME,
  SELECTED_ELEMENT_KEY,
  STORYBOARD_COMPONENT,
  STORYBOARD_TYPE,
  getMemoryStorageItem,
  removeMemoryStorageItem,
  setMemoryStorageItem,
} from './sessionStorageUtils';
import { TempoElement } from './tempoElement';

export const UNKNOWN_PARENT_COMPONENT = 'UnknownComponent';
export const TOP_LEVEL_PARENT_COMPONENT_TO_SKIP =
  'TOP_LEVEL_PARENT_COMPONENT_TO_SKIP';
export const EMPTY_TREE_CODEBASE_ID = 'EMPTY-TREE';

// Special codebase ID -> if set on the root node it's expected it doesn't get rendered
// This is used when there are multiple nodes under the root node that we want to return while we don't
// want to render the root node itself
export const SKIP_ROOT_CODEBASE_ID = 'SKIP-ROOT';

// Matches the interface on the frontend
export enum ExtractedPropType {
  LITERAL = 'LITERAL',
  FUNCTION = 'FUNCTION',
  JSON_OBJECT = 'JSON_OBJECT',
}
export interface NavTreeNode {
  parent?: NavTreeNode;
  children: NavTreeNode[];
  tempoElement: TempoElement;

  name: string;
  elementTagName?: string;
  isComponent?: boolean;
  level?: number;
  classList?: string[];
  directlySetClassList?: string[];
  scope?: any;

  // Used for drawing, moving, etc
  pageBoundingBox?: {
    pageX: number;
    pageY: number;
    width: number;
    height: number;
  };
  displayType?: string;
  positionType?: string;
  flexDirection?: string;
  floatVal?: string;

  // Not included, only for processing
  reactFiberNode?: any;

  // Does not include the "children" prop
  props?: {
    [key: string]: {
      value: any;
      type: ExtractedPropType;
    };
  };

  // These are the children that are literals with the
  // position they can be found
  literalChildren?: { index: number; value: any }[];
}

const extractPropsFromReactFiberNode = (reactFiberNode: any) => {
  if (!reactFiberNode?.element?.memoizedProps) {
    return {};
  }

  const props: any = {};
  Object.keys(reactFiberNode.element.memoizedProps).forEach((key) => {
    if (key === 'children') {
      return;
    }

    // Filter out known props
    if (KNOWN_ATTRIBUTES.has(key.toLowerCase())) {
      return;
    }

    let propValue = reactFiberNode.element.memoizedProps[key];

    // Filter out unknown classes
    if (key === 'className' && typeof propValue === 'string') {
      propValue = getAllUnknownClasesFromList(propValue.split(' ')).join(' ');
    }

    if (typeof propValue === 'function') {
      props[key] = {
        value: key,
        type: ExtractedPropType.FUNCTION,
      };
    } else if (typeof propValue === 'object') {
      try {
        props[key] = {
          value: JSON.stringify(propValue),
          type: ExtractedPropType.JSON_OBJECT,
        };
      } catch (e) {
        // skip this prop
      }
    } else {
      props[key] = {
        value: propValue,
        type: ExtractedPropType.LITERAL,
      };
    }
  });
  return props;
};

const extractLiteralChildrenFromReactFiberNode = (reactFiberNode: any) => {
  if (!reactFiberNode?.element?.memoizedProps?.children) {
    return [];
  }

  const literalChildren: {
    index: number;
    value: any;
  }[] = [];

  Array.from(reactFiberNode.element.memoizedProps.children || []).forEach(
    (childProp: any, index: number) => {
      if (typeof childProp !== 'object') {
        literalChildren.push({
          index,
          value: childProp,
        });
      }
    },
  );
  return literalChildren;
};

function selectorSafe(uniquePath: string): string {
  // Dictionary of replacements. You can expand this list as needed.
  const replacements: Record<string, string> = {
    '!': '_exclamation_',
    '@': '_at_',
    '#': '_hash_',
    $: '_dollar_',
    '%': '_percent_',
    '^': '_caret_',
    '&': '_and_',
    '*': '_asterisk_',
    '(': '_openParen_',
    ')': '_closeParen_',
    '+': '_plus_',
    '=': '_equals_',
    '[': '_openBracket_',
    ']': '_closeBracket_',
    '{': '_openBrace_',
    '}': '_closeBrace_',
    '|': '_pipe_',
    ';': '_semicolon_',
    ':': '_colon_',
    ',': '_comma_',
    '.': '_period_',
    '<': '_lessThan_',
    '>': '_greaterThan_',
    '/': '_slash_',
    '?': '_question_',
    '\\': '_backslash_',
    ' ': '_space_',
  };

  // Replace each character with its mapped value
  Object.keys(replacements).forEach((character) => {
    const regex = new RegExp('\\' + character, 'g');
    uniquePath = uniquePath.replace(regex, replacements[character]);
  });

  // Handle invalid starting characters
  uniquePath = uniquePath.replace(/^[0-9-]/, '_startNumOrHyphen_');

  // Lastly, replace any remaining non-alphanumeric characters just in case
  return uniquePath.replace(/[^a-zA-Z0-9_-]/g, '_');
}

/**
 * Nav node for a component that has no DOM element associated with it
 */
export const getNavNodeForVirtualComponent = (
  parent: NavTreeNode,
  componentName: string,
  componentInstanceId: string | undefined,
  uniquePath: string,
  scopeLookup: { [codebaseId: string]: any },
  storyboardId: string,
  reactFiberNode: any,
): NavTreeNode => {
  const navTreeNode: NavTreeNode = {
    parent: parent,
    children: [],
    classList: [],
    directlySetClassList: [],
    name: '',
    tempoElement: TempoElement.empty(),
  };

  navTreeNode.name = componentName;
  navTreeNode.isComponent = true;

  navTreeNode.tempoElement = new TempoElement(
    componentInstanceId,
    storyboardId,
    uniquePath,
  );

  navTreeNode.props = extractPropsFromReactFiberNode(reactFiberNode);
  navTreeNode.literalChildren =
    extractLiteralChildrenFromReactFiberNode(reactFiberNode);

  Object.keys(scopeLookup).forEach((codebaseId: string) => {
    if (navTreeNode.scope) {
      return;
    }

    if (
      scopeLookup[codebaseId].codebaseIds?.indexOf(componentInstanceId) > -1
    ) {
      navTreeNode.scope = scopeLookup[codebaseId];
    }
  });

  return navTreeNode;
};

export const buildNavForNode = (
  storyboardId: string,
  parent: NavTreeNode | undefined,
  node: any,
  uniquePathBase: string,
  uniquePathAddon: string,
  scopeLookup: { [codebaseId: string]: any },
  treeElements: { [codebaseId: string]: any },
  lookupIdToReactTreeMap: any,
  knownComponentNames: Set<string>,
  knownComponentInstanceNames: Set<string>,

  // Gets populated by this function, a lookup of element key -> list of element keys that represent this virtual component
  elementKeyToLookupList: { [elementKey: string]: string[] },
  elementKeyToNavNode: { [elementKey: string]: NavTreeNode },
): NavTreeNode | null => {
  if (!node) {
    return null;
  }

  if (isNodeOutline(node)) {
    return null;
  }

  if (isSkipNavTreeNode(node)) {
    return null;
  }

  if (['noscript', 'script'].includes(node?.tagName?.toLowerCase())) {
    return null;
  }

  const foundId = getCodebaseIdFromNode(node);
  const reactFiberLookupId = getUniqueLookupFromNode(node);

  // May 15, 2023 -> found bug where a random iframe was being added with the hot reloaded code
  // I think this is related to this bug:
  // https://github.com/facebook/create-react-app/issues/11880
  if (node?.tagName?.toLowerCase() === 'iframe') {
    if (!foundId) {
      node.remove();
      return null;
    }
  }

  let reactFiberNode: any = null;
  if (reactFiberLookupId) {
    reactFiberNode = lookupIdToReactTreeMap[reactFiberLookupId];
  }

  const boundingRect = node.getBoundingClientRect();
  const { top, left } = $(node).offset() || { top: 0, left: 0 };

  let parentToUse = parent;
  let uniquePathBaseToUse = uniquePathBase;

  //////////////////////////////////////////////////////////////////
  // Handle virtual components from the react fiber tree
  //////////////////////////////////////////////////////////////////

  // For outlines, components that are added need an outline around all the elements inside
  // Create lookups in local storage to keep track of this
  // Element keys of virtual components
  const virtualComponentElementKeys: string[] = [];

  // When there are react forward refs we want to collapse the node into the top level forward ref
  let componentNameToCollapseInto: string | undefined;
  let componentInstanceIdToCollapseInto: string | undefined;

  if (reactFiberNode && parent?.reactFiberNode) {
    // Traverse up the stack adding components to the tree until you hit this node's parent
    // Note, we have to account for other children that already performed this operation and added nodes to the tree
    let searchNode = reactFiberNode.parent;
    let possibleNodesToAdd: any[] = [];

    // This loop picks all the relevant nodes in between (ignoring if they are already added or not)
    while (searchNode) {
      if (searchNode === parent.reactFiberNode) {
        break;
      }

      // Sometimes components are named differently in the react fiber tree from the codebase, but we still want to include them
      // in the DOM tree if they are components defined in source files
      // E.g. in next JS if you create a <Link /> element it will be called "LinkComponent"
      const debugSourceFileInCodebase =
        searchNode?.element?._debugSource?.fileName &&
        !searchNode?.element?._debugSource?.fileName?.includes('node_modules');

      if (
        (searchNode.props?.tempoelementid ||
          searchNode.props?.['data-testid']) &&
        (knownComponentNames?.has(searchNode.name) ||
          knownComponentInstanceNames?.has(searchNode.name) ||
          debugSourceFileInCodebase)
      ) {
        possibleNodesToAdd.push(searchNode);
      }
      searchNode = searchNode.parent;
    }

    // Found the parent, traverse down the nodes, checking if that node was already added to the tree,
    // and adding it if it wasn't
    if (searchNode && possibleNodesToAdd.length) {
      let currentParent = parent;

      Array.from(possibleNodesToAdd)
        .reverse()
        .forEach((nodeToAdd: any) => {
          // If this is a forward ref just move forward in the tree without adding this element, but
          // but still label the next non-forward ref with this node's name & instance ID
          // However, only do this the first time (want the highest forward ref)
          if (
            nodeToAdd?.element?.elementType?.['$$typeof']?.toString() ===
            'Symbol(react.forward_ref)'
          ) {
            if (
              !componentNameToCollapseInto &&
              !componentInstanceIdToCollapseInto
            ) {
              componentInstanceIdToCollapseInto =
                nodeToAdd.props?.tempoelementid ||
                nodeToAdd.props?.['data-testid'];

              const referenceTreeElement =
                treeElements[componentInstanceIdToCollapseInto || ''];

              if (referenceTreeElement?.type === 'component-instance') {
                componentNameToCollapseInto =
                  referenceTreeElement.componentName;
              } else {
                componentNameToCollapseInto = nodeToAdd.name;
              }
            }

            return;
          }

          const matchingNavTreeNode = currentParent
            ? currentParent.children?.find(
                (child) => child.reactFiberNode === nodeToAdd,
              )
            : null;

          // Node already matches, increase level and continue
          if (matchingNavTreeNode) {
            currentParent = matchingNavTreeNode;

            if (currentParent.tempoElement) {
              virtualComponentElementKeys.push(
                currentParent.tempoElement.getKey(),
              );
            }

            // Increase the size of the bounding box for this element
            if (!matchingNavTreeNode.pageBoundingBox) {
              matchingNavTreeNode.pageBoundingBox = {
                pageX: left,
                pageY: top,
                width: boundingRect.width,
                height: boundingRect.height,
              };
            } else {
              const newRight = Math.max(
                matchingNavTreeNode.pageBoundingBox.pageX +
                  matchingNavTreeNode.pageBoundingBox.width,
                left + boundingRect.width,
              );
              const newLeft = Math.min(
                matchingNavTreeNode.pageBoundingBox.pageX,
                boundingRect.left,
              );
              const newTop = Math.min(
                matchingNavTreeNode.pageBoundingBox.pageY,
                boundingRect.top,
              );
              const newBottom = Math.max(
                matchingNavTreeNode.pageBoundingBox.pageY +
                  matchingNavTreeNode.pageBoundingBox.height,
                top + boundingRect.height,
              );

              matchingNavTreeNode.pageBoundingBox.pageX = newLeft;
              matchingNavTreeNode.pageBoundingBox.pageY = newTop;
              matchingNavTreeNode.pageBoundingBox.width = newRight - newLeft;
              matchingNavTreeNode.pageBoundingBox.height = newBottom - newTop;
            }
            return;
          } else {
            // Otherwise, create a new virtual node, add to parent and continue
            let componentName: string | undefined;
            let componentInstanceId: string | undefined;

            if (componentNameToCollapseInto) {
              componentName = componentInstanceIdToCollapseInto;
              componentInstanceId = componentInstanceIdToCollapseInto;
              componentNameToCollapseInto = undefined;
              componentInstanceIdToCollapseInto = undefined;
            } else {
              componentName = nodeToAdd.name;
              componentInstanceId =
                nodeToAdd.props?.tempoelementid ||
                nodeToAdd.props?.['data-testid'];
            }

            // Update the unique path and use it
            uniquePathBaseToUse = selectorSafe(
              `${uniquePathBaseToUse}-${currentParent?.children?.length || 0}`,
            );
            const newVirtualComponent = getNavNodeForVirtualComponent(
              currentParent,
              nodeToAdd.name,
              componentInstanceId,
              uniquePathBaseToUse,
              scopeLookup,
              storyboardId,
              nodeToAdd,
            );
            currentParent.children.push(newVirtualComponent);
            currentParent = newVirtualComponent;

            virtualComponentElementKeys.push(
              newVirtualComponent.tempoElement.getKey(),
            );
            elementKeyToNavNode[newVirtualComponent.tempoElement.getKey()] =
              newVirtualComponent;

            // Set the bounding box for the new virtual component
            newVirtualComponent.pageBoundingBox = {
              pageX: left,
              pageY: top,
              width: boundingRect.width,
              height: boundingRect.height,
            };
          }
        });

      parentToUse = currentParent;
    }
  }

  // This node corresponds to the DOM element, not any components, unless we are collapsing into the component
  const navTreeNode: NavTreeNode = {
    parent: parentToUse,
    children: [],
    classList: getAllUnknownClasses(node),
    directlySetClassList: [],
    name: '',
    tempoElement: TempoElement.empty(),
  };

  parentToUse?.children?.push(navTreeNode);
  navTreeNode.name = componentNameToCollapseInto || node.tagName;
  navTreeNode.elementTagName = node.tagName;

  // These are only forward ref components, all other components are added as virtual components
  navTreeNode.isComponent = Boolean(componentInstanceIdToCollapseInto);

  const uniquePathForNode = selectorSafe(
    `${uniquePathBaseToUse}${uniquePathAddon}`,
  );

  const codebaseId = componentInstanceIdToCollapseInto || foundId || undefined;
  navTreeNode.tempoElement = new TempoElement(
    codebaseId,
    storyboardId,
    uniquePathForNode,
  );

  const nodeElementKey = navTreeNode.tempoElement.getKey();
  // Using the virtualComponentElementKeys, set the elementKey in a list for this element
  virtualComponentElementKeys.forEach((elementKey: any) => {
    if (elementKeyToLookupList[elementKey]) {
      elementKeyToLookupList[elementKey].push(nodeElementKey);
    } else {
      elementKeyToLookupList[elementKey] = [nodeElementKey];
    }
  });

  // Set the lookup list for the specific node itself as well
  elementKeyToLookupList[nodeElementKey] = [nodeElementKey];

  // Add the element key to the class to help with referencing
  // Note - even if there is no codebase ID we still mark it as something processed in the nav tree
  addElementKeyAsClass({ node, safeElementKey: nodeElementKey, codebaseId });

  const treeElementForNode = treeElements[navTreeNode.tempoElement.codebaseId];
  if (treeElementForNode) {
    const removableClasses = new Set(
      treeElementForNode?.removableClasses || [],
    );
    navTreeNode.directlySetClassList = navTreeNode.classList?.filter((cls) => {
      return removableClasses.has(cls);
    });
  }

  navTreeNode.reactFiberNode = reactFiberNode;
  navTreeNode.props = extractPropsFromReactFiberNode(reactFiberNode);
  navTreeNode.literalChildren =
    extractLiteralChildrenFromReactFiberNode(reactFiberNode);

  navTreeNode.pageBoundingBox = {
    pageX: left,
    pageY: top,
    width: boundingRect.width,
    height: boundingRect.height,
  };
  navTreeNode.displayType = cssEval(node, 'display');
  navTreeNode.positionType = cssEval(node, 'position');
  navTreeNode.flexDirection = cssEval(node, 'flex-direction');
  navTreeNode.floatVal = cssEval(node, 'float');

  if (navTreeNode.tempoElement.codebaseId) {
    Object.keys(scopeLookup).forEach((codebaseId: string) => {
      if (navTreeNode.scope) {
        return;
      }

      if (
        scopeLookup[codebaseId].codebaseIds?.indexOf(
          navTreeNode.tempoElement.codebaseId,
        ) > -1
      ) {
        navTreeNode.scope = scopeLookup[codebaseId];
      }
    });
  }

  // Only parse children for non-svg elements
  if (node.children && node.tagName !== 'svg') {
    let indexForUniqueness = 0;

    Array.from(node.children).forEach((child: any) => {
      buildNavForNode(
        storyboardId,
        navTreeNode,
        child,
        uniquePathForNode,
        `-${indexForUniqueness}`,
        scopeLookup,
        treeElements,
        lookupIdToReactTreeMap,
        knownComponentNames,
        knownComponentInstanceNames,
        elementKeyToLookupList,
        elementKeyToNavNode,
      );

      indexForUniqueness += 1;
    });
  }

  elementKeyToNavNode[nodeElementKey] = navTreeNode;

  // This is the top-level node
  if (!parentToUse) {
    let newNavTree = filterOutNodesWithoutCodebaseId(
      navTreeNode,
      elementKeyToNavNode,
      treeElements,
      storyboardId,
    );

    return newNavTree;
  }

  return null;
};

const filterOutNodesWithoutCodebaseId = (
  finishedNavTree: NavTreeNode,
  elementKeyToNavNode: { [elementKey: string]: NavTreeNode },
  treeElements: { [codebaseId: string]: any },
  storyboardId: string,
): NavTreeNode => {
  let treeToReturn: NavTreeNode = finishedNavTree;

  const storyboardType = getMemoryStorageItem(STORYBOARD_TYPE) || 'APPLICATION';
  const storyboardSavedComponentFile = getMemoryStorageItem(
    SAVED_STORYBOARD_COMPONENT_FILENAME,
  );

  const originalStoryboardUrl = getMemoryStorageItem(ORIGINAL_STORYBOARD_URL);

  const userNavigatedToNewRoute =
    originalStoryboardUrl &&
    !window.location.href.includes(originalStoryboardUrl);

  /**
   * Returns whether the given tree element is in the top-level file of the storyboard
   * Note - for saved components the top-level file is the saved component file
   */
  const isElementDirectlyInStoryboard = (node: NavTreeNode) => {
    const filename = treeElements[node.tempoElement.codebaseId]?.filename;

    // For stories, just filter for anything not in _app or _document
    if (
      storyboardType === 'STORY' &&
      filename &&
      !filename.includes('_app') &&
      !filename.includes('_document')
    ) {
      return true;
    }

    // Special case -> if the parent is the body element this might be in a portal
    // go all the way up the react fiber tree and see if there are any elements
    // that are in the storyboard
    if (node.parent?.name === 'BODY') {
      let parentFiberNode = node.reactFiberNode?.parent;
      while (parentFiberNode) {
        const codebaseId =
          parentFiberNode?.props?.tempoelementid ||
          parentFiberNode?.props?.['data-testid'] ||
          '';

        if (codebaseId) {
          const treeElementFilename = treeElements[codebaseId]?.filename;
          const valid =
            Boolean(treeElementFilename?.includes('tempobook/storyboards')) ||
            Boolean(
              treeElementFilename &&
                treeElementFilename === storyboardSavedComponentFile,
            );

          if (valid) {
            return true;
          }
        }

        parentFiberNode = parentFiberNode?.parent;
      }
    }

    // For everything else, filter anything that is not in the storyboard itself
    return (
      Boolean(filename?.includes('tempobook/storyboards')) ||
      Boolean(filename && filename === storyboardSavedComponentFile)
    );
  };

  const processNode = (
    node: NavTreeNode,
    elementInStoryboardFound: boolean,
  ) => {
    // Process the children first
    for (let i = node.children.length - 1; i >= 0; i--) {
      processNode(
        node.children[i],
        elementInStoryboardFound || isElementDirectlyInStoryboard(node),
      );
    }

    // Product decision: Filter out nodes that don't exist in storyboard file for the corresponding component URL
    //
    // Historical context:
    // Dec 14 - a bug was found where in cases that components were dynamically loaded (e.g. in Next JS _app.tsx), when you click
    // on the top level component it would point to this location in the codebase:
    //
    // function MyApp({ Component, pageProps: { session, ...pageProps } }: AppProps) {
    //   return (
    //     <SessionProvider session={session}>
    //       <Component {...pageProps} />
    //       <Analytics />
    //     </SessionProvider>
    //   );
    // }
    //
    // This was especially an issue for component storyboards. Thus the decision was made to hide any top-level components or divs
    // that are not in the storyboard file
    const inComponentStoryboardAndSkip =
      storyboardType !== 'APPLICATION' &&
      !userNavigatedToNewRoute &&
      !elementInStoryboardFound &&
      !isElementDirectlyInStoryboard(node);

    // If this node doesn't have a codebaseId, move its children to its parent
    if (
      !node.tempoElement.codebaseId?.startsWith('tempo-') ||
      inComponentStoryboardAndSkip
    ) {
      if (node.parent) {
        // Move the children in the spot where the node was
        const childrenToMove = node.children;
        const indexOfNodeInParent = node.parent.children?.indexOf(node);

        node.parent.children.splice(indexOfNodeInParent, 1, ...childrenToMove);

        // Change the parent of all the children to the new parent
        childrenToMove.forEach((child) => {
          child.parent = node.parent;
        });

        // Remove the node from the known nodes
        delete elementKeyToNavNode[node.tempoElement.getKey()];
      } else if (node.children.length === 1) {
        // This is the top-level node, move it down
        treeToReturn = node.children[0];
        delete elementKeyToNavNode[node.tempoElement.getKey()];

        treeToReturn.parent = undefined;
      } else if (node.children.length === 0) {
        // 0 children, no nav tree to return
        treeToReturn = {
          children: [],
          tempoElement: new TempoElement(
            EMPTY_TREE_CODEBASE_ID,
            storyboardId,
            '1',
          ),
          name: '',
        };
        delete elementKeyToNavNode[node.tempoElement.getKey()];
      } else {
        // 2+ children, return this node, but make the codebase ID one to skip
        node.tempoElement = new TempoElement(
          SKIP_ROOT_CODEBASE_ID,
          node.tempoElement.storyboardId,
          node.tempoElement.uniquePath,
        );
        delete elementKeyToNavNode[node.tempoElement.getKey()];
      }
    }
  };

  processNode(finishedNavTree, false);

  const postProcess = (node: NavTreeNode, level: number) => {
    // Remove the react fiber node after processing
    delete node['reactFiberNode'];

    node.level = level;
    node.children.forEach((child) => {
      postProcess(
        child,
        node.tempoElement.codebaseId === SKIP_ROOT_CODEBASE_ID
          ? level
          : level + 1,
      );
    });
  };

  postProcess(treeToReturn, 0);
  return treeToReturn;
};

interface CallbackOptions {
  callbackFn: Function;
  state: {
    selectedElementKey: string;
    multiSelectedElementKeys: string[];
  };
}

export const addNavTreeBuiltCallback = (
  callbackToAdd: CallbackOptions,
): void => {
  const { callbackFn, state } = callbackToAdd;
  const callbacks: CallbackOptions[] =
    getMemoryStorageItem(NAV_TREE_CALLBACKS) || [];

  // Sort the multiSelectedElementKeys for consistency before adding
  state.multiSelectedElementKeys = (
    state.multiSelectedElementKeys || []
  ).sort();

  const existingCallback = callbacks.find(
    (callback) =>
      callback.callbackFn.toString() === callbackFn.toString() &&
      callback.state.selectedElementKey === state.selectedElementKey &&
      callback.state.multiSelectedElementKeys.join(',') ===
        state.multiSelectedElementKeys.join(','),
  );

  if (existingCallback) {
    return;
  }

  callbacks.push(callbackToAdd);
  setMemoryStorageItem(NAV_TREE_CALLBACKS, callbacks);
};

export const runNavTreeBuiltCallbacks = (): void => {
  const callbacks: CallbackOptions[] =
    getMemoryStorageItem(NAV_TREE_CALLBACKS) || [];

  if (!callbacks.length) {
    return;
  }

  const currentSelectedKey = getMemoryStorageItem(SELECTED_ELEMENT_KEY);
  const multiSelectedElementKeys = (
    getMemoryStorageItem(MULTI_SELECTED_ELEMENT_KEYS) || []
  ).sort();

  callbacks.forEach((callback) => {
    const { callbackFn, state } = callback;

    if (
      state.selectedElementKey === currentSelectedKey &&
      state.multiSelectedElementKeys.join(',') ===
        multiSelectedElementKeys.join(',')
    ) {
      callbackFn();
    }
  });

  removeMemoryStorageItem(NAV_TREE_CALLBACKS);
};
