  // @ts-ignore
  window.applyChangeItemToDocument = async (
    parentPort: any,
    storyboardId: string,
    changeItem: AnyChangeLedgerItem,
  ) => {
    const { sendNewNavTree } = applyChangeItemToDocument(
      parentPort,
      storyboardId,
      changeItem,
    );

    // Update the nav tree & outlines
    if (sendNewNavTree) {
      buildAndSendNavTree(parentPort, storyboardId);
    }
    updateOutlines(parentPort, storyboardId);
  };

  // @ts-ignore
  window.updateCodebaseIds = (
    parentPort: any,
    storyboardId: string,
    prevIdToNewIdMap: { [prevCodebaseId: string]: string },
    newTreeElementLookup: any,
    newScopeLookup: any,
  ) => {
    const sendNewNavTree = updateCodebaseIds(
      parentPort,
      prevIdToNewIdMap,
      true,
    );

    if (sendNewNavTree) {
      buildAndSendNavTree(
        parentPort,
        storyboardId,
        newTreeElementLookup,
        newScopeLookup,
      );
    }

    updateOutlines(parentPort, storyboardId);
  };

  // @ts-ignore
