import { v4 as uuidv4 } from 'uuid';

// These constants match what tempo-api has
export const WRAP_IN_DIV_PLACEHOLDER_CODEBASE_ID =
  'tempo-wrap-in-div-placeholder';
export const DUPLICATE_PLACEHOLDER_PREFIX = 'tempo-duplicate-placeholder-';
export const ADD_JSX_PREFIX = 'tempo-add-jsx-placeholder-';

// Stored in memory storage, used to keep track of some possible add class instant
// updates that need to be re-applied after a hot reload
// (e.g. when the additional) instant updates happened during flushing
export const ADD_CLASS_INSTANT_UPDATE_QUEUE = 'ADD_CLASS_INSTANT_UPDATE_QUEUE';

export const TEMPORARY_STYLING_CLASS_NAME = 'arb89-temp-styling';

const getTopLevelCodebaseIdForComponent = (componentId: string): string => {
  let topLevelCodebaseId: any = null;
  let minNumberParents = Infinity;
  $(`.component-${componentId}`).each((index: any, element: any) => {
    if ($(element).parents().length < minNumberParents) {
      minNumberParents = $(element).parents().length;
      topLevelCodebaseId = getCodebaseIdFromNode(element);
    }
  });

  return topLevelCodebaseId;
};

const makeid = (length: number): string => {
  let result = '';
  const characters =
    'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  const charactersLength = characters.length;
  let counter = 0;
  while (counter < length) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
    counter += 1;
  }
  return result;
};

const addOrEditCSSRule = (selector: string, rules: string, id?: string) => {
  var styleEl = document.createElement('style');

  if (id) {
    const existingElement = document.getElementById(id);
    if (existingElement) {
      existingElement.remove();
    }

    styleEl.id = id;
  }

  // Append <style> element to <head>
  document.head.appendChild(styleEl);

  var styleSheet: any = styleEl.sheet;

  if (styleSheet.insertRule) {
    // All browsers, except IE before version 9
    styleSheet.insertRule(
      selector + '{' + rules + '}',
      styleSheet.cssRules.length,
    );
  } else if (styleSheet.addRule) {
    // IE before version 9
    styleSheet.addRule(selector, rules, styleSheet.rules.length);
  }
};

export const applyChangeItemToDocument = (
  parentPort: any,
  storyboardId: string,
  plainChangeItem: AnyChangeLedgerItem,
): { sendNewNavTree: boolean; instantUpdateSuccessful: boolean } => {
  if (!plainChangeItem || !plainChangeItem.type) {
    return { sendNewNavTree: false, instantUpdateSuccessful: false };
  }

  const changeItem = reconstructChangeLedgerClass(
    plainChangeItem,
  ) as AnyChangeLedgerItem;

  let extraInstantUpdateData: any = {};
  let instantUpdateSuccessful = false;

  // The display: none rule is needed for a lot of instant updates, so create it if it doesn't exist
  if (!document.getElementById(TEMPO_DISPLAY_NONE_UNTIL_REFRESH_CLASS)) {
    addOrEditCSSRule(
      `.${TEMPO_DISPLAY_NONE_UNTIL_REFRESH_CLASS}`,
      'display: none !important',
      TEMPO_DISPLAY_NONE_UNTIL_REFRESH_CLASS,
    );
  }

  let sendNewNavTree = false;
  if (changeItem.type === ChangeType.ADD_JSX) {
    const castChangeItem = changeItem as AddJsxChange;
    const changeFields = castChangeItem.changeFields;

    const newAddedIds = [];

    if (changeFields.htmlForInstantUpdate) {
      const elementToAdd = $(changeFields.htmlForInstantUpdate);
      elementToAdd.attr(TEMPO_DELETE_AFTER_REFRESH, 'true');
      elementToAdd.attr(TEMPO_INSTANT_UPDATE, 'true'); // So that the DOM tree refresh doesn't get triggered
      elementToAdd.attr(TEMPO_OUTLINE_UNTIL_REFESH, 'true');

      const ID_FOR_ELEMENT = `${ADD_JSX_PREFIX}${uuidv4()}`;
      elementToAdd.attr(TEMPO_ELEMENT_ID, ID_FOR_ELEMENT);
      elementToAdd.addClass(ID_FOR_ELEMENT);

      newAddedIds.push(ID_FOR_ELEMENT);

      $(`.${changeFields.codebaseIdToAddTo}`).each((index: any, item) => {
        if (changeFields.afterCodebaseId) {
          const afterElement = $(`.${changeFields.afterCodebaseId}`);
          if (!afterElement?.length) {
            return;
          }

          elementToAdd.insertAfter(afterElement.first());
        } else if (changeFields.beforeCodebaseId) {
          const beforeElement = $(`.${changeFields.beforeCodebaseId}`);
          if (!beforeElement?.length) {
            return;
          }
          elementToAdd.insertBefore(beforeElement.first());
        } else {
          $(item).append(elementToAdd);
        }

        sendNewNavTree = true;
        instantUpdateSuccessful = true;
      });
    }

    extraInstantUpdateData['newAddedIds'] = newAddedIds;
  } else if (changeItem.type === ChangeType.MOVE_JSX) {
    const castChangeItem: MoveJsxChange = changeItem as MoveJsxChange;

    // Find each element that matches the jsxCodebaseId
    const sourceElements: JQuery<any>[] = [];

    // See if direct matches work first
    if ($(`.${castChangeItem.changeFields.codebaseIdToMove}`).length > 0) {
      $(`.${castChangeItem.changeFields.codebaseIdToMove}`).each(
        (index: any, element: any) => {
          sourceElements.push($(element));
        },
      );
    } else {
      // Try to find it by the component ID
      let topLevelCodebaseId = getTopLevelCodebaseIdForComponent(
        castChangeItem.changeFields.codebaseIdToMove || '',
      );

      if (topLevelCodebaseId) {
        $(`.${topLevelCodebaseId}`).each((index: any, element: any) => {
          sourceElements.push($(element));
        });
      }
    }

    // If the container is a component, drop into the codebaseId of the top-most child div
    let containerCodebaseId =
      getTopLevelCodebaseIdForComponent(
        castChangeItem.changeFields.codebaseIdToMoveTo || '',
      ) || castChangeItem.changeFields.codebaseIdToMoveTo;

    // For each source element, find the new matching parent element
    const newParentElements: any[] = [];
    sourceElements.forEach((element) => {
      let newParentElement = null;

      // For each parent, try to see if it either matches or contains the new parent
      let parentElement = element.parent();
      while (parentElement.length) {
        // If the parent directly matches, this is it
        if (parentElement.hasClass(containerCodebaseId)) {
          newParentElement = parentElement;
          break;
        }

        // Check children that match the codebase ID to drop into
        const matchingChildren = parentElement.find(`.${containerCodebaseId}`);
        if (matchingChildren.length) {
          // TODO: What if this matches more than one?
          newParentElement = matchingChildren.first();
          break;
        }

        parentElement = parentElement.parent();
      }

      if (!newParentElement) {
        newParentElements.push(null);
        return;
      }

      newParentElements.push(newParentElement);
    });

    // For each child/parentElement pair, move the child to the new parent
    sourceElements.forEach((element, index: number) => {
      const newParentElement = newParentElements[index];

      if (!newParentElement.length) {
        console.log('Could not find new parent element for instant update');
        return;
      }

      sendNewNavTree = true;
      instantUpdateSuccessful = true;

      element.attr(TEMPO_INSTANT_UPDATE, 'true');

      // If the parent hasn't changed, just move it, otherwise clone it and create a new one in the new spot
      let useClone = !newParentElement.is(element.parent());

      // So that nextjs hot reloading works, simply hide the element and clone it into the new spot
      let cloneElement;
      if (useClone) {
        cloneElement = element.clone();
        cloneElement.attr(TEMPO_DELETE_AFTER_REFRESH, 'true');

        element.addClass(TEMPO_DISPLAY_NONE_UNTIL_REFRESH_CLASS);
        element.attr(TEMPO_DO_NOT_SHOW_IN_NAV_UNTIL_REFRESH, 'true');
      }

      if (castChangeItem.changeFields.afterCodebaseId) {
        const afterIdToUse =
          getTopLevelCodebaseIdForComponent(
            castChangeItem.changeFields.afterCodebaseId,
          ) || castChangeItem.changeFields.afterCodebaseId;
        const afterElement = newParentElement.children(`.${afterIdToUse}`);
        if (afterElement.length) {
          if (useClone && cloneElement) {
            cloneElement.insertAfter(afterElement.first());
          } else {
            element.insertAfter(afterElement.first());
          }

          return;
        }
      }

      if (castChangeItem.changeFields.beforeCodebaseId) {
        const beforeIdToUse =
          getTopLevelCodebaseIdForComponent(
            castChangeItem.changeFields.beforeCodebaseId,
          ) || castChangeItem.changeFields.beforeCodebaseId;
        const beforeElement = newParentElement.children(`.${beforeIdToUse}`);
        if (beforeElement.length) {
          if (useClone && cloneElement) {
            cloneElement.insertBefore(beforeElement.first());
          } else {
            element.insertBefore(beforeElement.first());
          }
          return;
        }
      }

      if (useClone && cloneElement) {
        cloneElement.appendTo(newParentElement);
      } else {
        element.appendTo(newParentElement);
      }
    });
  } else if (changeItem.type === ChangeType.REMOVE_JSX) {
    const castChangeItem: RemoveJsxChange = changeItem as RemoveJsxChange;

    const parentToElementKeysRemoved: any = {};

    castChangeItem.changeFields.codebaseIdsToRemove.forEach(
      (codebaseId: string) => {
        // See if direct matches work first
        let codebaseIdToRemove;
        if ($(`.${codebaseId}`).length > 0) {
          codebaseIdToRemove = codebaseId;
        } else {
          // Try to find it by the component ID
          let topLevelCodebaseId = getTopLevelCodebaseIdForComponent(
            codebaseId || '',
          );

          if (!topLevelCodebaseId) {
            console.log('Could not find component element for instant update');
            return false;
          }

          codebaseIdToRemove = topLevelCodebaseId;
        }

        // For each item that is removed, save the inner HTML in case it gets deleted and we want to undo
        $(`.${codebaseIdToRemove}`).each((index: any, item) => {
          const elementKeyRemoved = getElementKeyFromNode(item);
          const parentElementKey = getElementKeyFromNode(item.parentElement);

          if (elementKeyRemoved && parentElementKey) {
            if (!parentToElementKeysRemoved[parentElementKey]) {
              parentToElementKeysRemoved[parentElementKey] = [];
            }

            parentToElementKeysRemoved[parentElementKey].push({
              outerHTML: item.outerHTML,
              elementKeyRemoved,
            });
          }

          item.classList.add(TEMPO_DISPLAY_NONE_UNTIL_REFRESH_CLASS);
          item.setAttribute(TEMPO_DO_NOT_SHOW_IN_NAV_UNTIL_REFRESH, 'true');

          sendNewNavTree = true;
          instantUpdateSuccessful = true;
        });
      },
    );

    extraInstantUpdateData.parentToElementKeysRemoved =
      parentToElementKeysRemoved;
  } else if (
    changeItem.type === ChangeType.ADD_CLASS ||
    changeItem.type === ChangeType.STYLING
  ) {
    let className,
      cssEquivalent,
      codebaseIdToAddClass,
      temporaryClass,
      codebaseClassName,
      modifiers;

    if (changeItem.type === ChangeType.ADD_CLASS) {
      const castChangeItem: AddClassChange = changeItem as AddClassChange;
      codebaseClassName = castChangeItem.changeFields.className;
      className = castChangeItem.changeFields.className;
      cssEquivalent = castChangeItem.changeFields.cssEquivalent;
      codebaseIdToAddClass = castChangeItem.changeFields.codebaseIdToAddClass;
      temporaryClass = castChangeItem.changeFields.temporaryOnly;
      modifiers = castChangeItem.changeFields.modifiers;
      if (temporaryClass) {
        className = TEMPORARY_STYLING_CLASS_NAME;
      }
    } else {
      // As of March 6, 2024 we only support tailwind STYLING changes, so treat them as adding a class
      const castChangeItem: StylingChangeFields =
        changeItem.changeFields as StylingChangeFields;
      className = '';
      cssEquivalent = Object.keys(castChangeItem.stylingChanges)
        .map((key) => {
          if (castChangeItem.stylingChanges[key] === DELETE_STYLE_CONSTANT) {
            return `${camelToSnakeCase(key)}: unset !important;`;
          }

          return `${camelToSnakeCase(key)}: ${
            castChangeItem.stylingChanges[key]
          };`;
        })
        .join('');
      codebaseIdToAddClass = castChangeItem.codebaseId;
      modifiers = castChangeItem.modifiers;
    }
    const SAFE_CLASSNAME_REGEX = /[^A-Za-z0-9_-]/g;

    // Escape any custom classes
    let classToAdd = (className || '')
      .replace(SAFE_CLASSNAME_REGEX, '-') // Replace any non-alphanumeric characters with '-'
      .replace(/^\d/, '-$&'); // If the class starts with a digit, prepend with '-'

    // Instead of adding the class name, generate a new class and set the
    // css equivalent values inside it
    // This class will be deleted after a hot reload
    // Note - for temporary classes we want to explicitly use the same class
    if (cssEquivalent && !temporaryClass) {
      const msSinceJan1 = Date.now() - 1704067200000;
      classToAdd = `${TEMPO_INSTANT_UPDATE_STYLING_PREFIX}${msSinceJan1}-${classToAdd}`;
    }

    if (classToAdd) {
      if (!temporaryClass) {
        // Clear the temporary class on this element if it has it
        $(`.${codebaseIdToAddClass}`).removeClass(TEMPORARY_STYLING_CLASS_NAME);
      }

      if (cssEquivalent) {
        if (modifiers && modifiers.length > 0) {
          const CSS_PSEUDO_MODIFIERS = [
            'hover',
            'required',
            'focus',
            'active',
            'invalid',
            'disabled',
          ];

          const pseudoModifiers = modifiers.filter((modifier) =>
            CSS_PSEUDO_MODIFIERS.includes(modifier),
          );
          const pseudoModifiersSuffix = pseudoModifiers.join(':');

          if (pseudoModifiers.length > 0) {
            const modifierClass = `${classToAdd}:${pseudoModifiersSuffix}`;
            addOrEditCSSRule(`.${modifierClass}`, cssEquivalent, modifierClass);
          } else {
            addOrEditCSSRule(`.${classToAdd}`, cssEquivalent, classToAdd);
          }

          const forceClasses = modifiers
            .map((modifier) => `.tempo-force-${modifier}`)
            .join('');
          const instantUpdateForForceClass = `${classToAdd}${forceClasses}`;

          addOrEditCSSRule(
            `.${instantUpdateForForceClass}`,
            cssEquivalent,
            instantUpdateForForceClass,
          );
        } else {
          addOrEditCSSRule(`.${classToAdd}`, cssEquivalent, classToAdd);
        }
      }

      const currentAddClassValues =
        getMemoryStorageItem(ADD_CLASS_INSTANT_UPDATE_QUEUE) || [];

      // See if direct matches work first
      if ($(`.${codebaseIdToAddClass}`).length > 0) {
        $(`.${codebaseIdToAddClass}`).addClass(classToAdd);

        instantUpdateSuccessful = true;

        currentAddClassValues.push({
          codebaseId: codebaseIdToAddClass,
          className: classToAdd,
        });
      } else {
        // Try to find it by the component ID
        let topLevelCodebaseId = getTopLevelCodebaseIdForComponent(
          codebaseIdToAddClass || '',
        );

        if (topLevelCodebaseId && $(`.${topLevelCodebaseId}`).length > 0) {
          instantUpdateSuccessful = true;
          $(`.${topLevelCodebaseId}`).addClass(classToAdd);

          currentAddClassValues.push({
            codebaseId: topLevelCodebaseId,
            className: classToAdd,
          });
        }
      }

      setMemoryStorageItem(
        ADD_CLASS_INSTANT_UPDATE_QUEUE,
        currentAddClassValues,
      );

      extraInstantUpdateData.addedClass = classToAdd;
      extraInstantUpdateData.codebaseAddedClass = codebaseClassName;
    }
  } else if (changeItem.type === ChangeType.REMOVE_CLASS) {
    const removeClassChangeFields =
      changeItem.changeFields as RemoveClassChangeFields;

    // See if direct matches work first
    if ($(`.${removeClassChangeFields.codebaseIdToRemoveClass}`).length > 0) {
      $(`.${removeClassChangeFields.codebaseIdToRemoveClass}`).removeClass(
        removeClassChangeFields.className,
      );

      instantUpdateSuccessful = true;
    } else {
      // Try to find it by the component ID
      let topLevelCodebaseId = getTopLevelCodebaseIdForComponent(
        removeClassChangeFields.codebaseIdToRemoveClass || '',
      );

      if (topLevelCodebaseId && $(`.${topLevelCodebaseId}`).length > 0) {
        instantUpdateSuccessful = true;
        $(`.${topLevelCodebaseId}`).removeClass(
          removeClassChangeFields.className,
        );
      }
    }
  } else if (changeItem.type === ChangeType.WRAP_DIV) {
    const changeFields = changeItem.changeFields as WrapDivChangeFields;
    const codebaseIdsToWrap = changeFields.codebaseIdsToWrap;

    const firstCodebaseId = codebaseIdsToWrap[0];

    // We assume the other codebase IDs are siblings of this codebase ID, so we
    // find each instance of the first one and include the other items that match in it
    // If the other items aren't all found, we do not wrap and let hot reload handle it
    $(`.${firstCodebaseId}`).each((index: any, item) => {
      const otherCodebaseIds = codebaseIdsToWrap.slice(1);
      // For each codebase ID in otherCodebaseIds, retrieve the element that is a sibling of item
      const siblings = $(item).siblings();
      const allItemsToAddToNewDiv = [item];

      let earliestItem = item;
      let earliestIndex = $(item).index();

      otherCodebaseIds.forEach((codebaseId) => {
        const foundSibling = siblings.filter(`.${codebaseId}`).get(0);
        if (foundSibling) {
          allItemsToAddToNewDiv.push(foundSibling);
          const index = $(foundSibling).index();
          if (index < earliestIndex) {
            earliestItem = foundSibling;
            earliestIndex = index;
          }
        }
      });

      // TODO: What to do if they all can't be found?
      if (allItemsToAddToNewDiv.length !== codebaseIdsToWrap.length) {
        // For now, just add the ones that were found
      }

      // Create a div with a clone of the item, while hiding the item
      // When the hot reload happens the clone gets deleted and the item is shown again
      const newDiv = document.createElement('div');
      newDiv.className = WRAP_IN_DIV_PLACEHOLDER_CODEBASE_ID;
      newDiv.setAttribute(TEMPO_INSTANT_UPDATE, 'true'); // So that the DOM tree refresh doesn't get triggered
      newDiv.setAttribute(
        'tempoelementid',
        WRAP_IN_DIV_PLACEHOLDER_CODEBASE_ID,
      );
      newDiv.setAttribute('data-testid', WRAP_IN_DIV_PLACEHOLDER_CODEBASE_ID);
      newDiv.setAttribute(TEMPO_DELETE_AFTER_REFRESH, 'true');
      allItemsToAddToNewDiv.forEach((elem) => {
        newDiv.appendChild(elem.cloneNode(true));

        elem.classList.add(TEMPO_DISPLAY_NONE_UNTIL_REFRESH_CLASS);
        elem.setAttribute(TEMPO_DO_NOT_SHOW_IN_NAV_UNTIL_REFRESH, 'true');
      });

      // Insert the new div right before the first item
      earliestItem.insertAdjacentElement('beforebegin', newDiv);

      sendNewNavTree = true;
      instantUpdateSuccessful = true;
    });
  } else if (changeItem.type === ChangeType.DUPLICATE) {
    const changeFileds = changeItem.changeFields as DuplicateChangeFields;
    const codebaseIdsToDuplicate = changeFileds.codebaseIdsToDuplicate;

    codebaseIdsToDuplicate.forEach((codebaseIdToDuplicate) => {
      $(`.${codebaseIdToDuplicate}`).each((index: any, item) => {
        const clonedNode = item.cloneNode(true) as HTMLElement;

        clonedNode.setAttribute(TEMPO_DELETE_AFTER_REFRESH, 'true');
        clonedNode.setAttribute(TEMPO_INSTANT_UPDATE, 'true'); // So that the DOM tree refresh doesn't get triggered

        // Set up all the correct duplicated codebase IDs
        clonedNode.setAttribute(
          'tempoelementid',
          `${DUPLICATE_PLACEHOLDER_PREFIX}${codebaseIdToDuplicate}`,
        );
        clonedNode.setAttribute(
          'data-testid',
          `${DUPLICATE_PLACEHOLDER_PREFIX}${codebaseIdToDuplicate}`,
        );
        clonedNode.classList.add(
          DUPLICATE_PLACEHOLDER_PREFIX + codebaseIdToDuplicate,
        );
        clonedNode.classList.remove(codebaseIdToDuplicate);

        let children = Array.from(clonedNode.children);
        while (children.length) {
          const child = children.pop();
          if (!child) {
            continue;
          }

          const codebaseId =
            child.getAttribute('tempoelementid') ||
            child.getAttribute('data-testid');
          if (!codebaseId) {
            continue;
          }

          child.setAttribute(
            'tempoelementid',
            `${DUPLICATE_PLACEHOLDER_PREFIX}${codebaseId}`,
          );
          child.setAttribute(
            'data-testid',
            `${DUPLICATE_PLACEHOLDER_PREFIX}${codebaseId}`,
          );
          child.classList.remove(codebaseId);
          child.classList.add(DUPLICATE_PLACEHOLDER_PREFIX + codebaseId);
          child.setAttribute(TEMPO_INSTANT_UPDATE, 'true');

          children.push(...Array.from(child.children));
        }

        // Add the clones node right after the found node
        item.insertAdjacentElement('afterend', clonedNode);

        sendNewNavTree = true;
        instantUpdateSuccessful = true;
      });
    });
  } else if (changeItem.type === ChangeType.CHANGE_TAG) {
    const changeFields = changeItem.changeFields as ChangeTagChangeFields;

    $(`.${changeFields.codebaseIdToChange}`).each((index: any, item) => {
      const $newElement = $(
        '<' + changeFields.newTagName + '></' + changeFields.newTagName + '>',
      );
      $newElement.attr(TEMPO_INSTANT_UPDATE, 'true'); // So that the DOM tree refresh doesn't get triggered
      $newElement.attr(TEMPO_DELETE_AFTER_REFRESH, 'true');

      const $item = $(item);

      // Copy all attributes from the original element to the new element
      $.each($item[0].attributes, function () {
        $newElement.attr(this.name, this.value);
      });

      $item.contents().clone(true, true).appendTo($newElement);

      // Add right before the cloned item so the unique path stays the same
      $item.before($newElement);

      // Hide the original item
      $item.addClass(TEMPO_DISPLAY_NONE_UNTIL_REFRESH_CLASS);
      $item.attr(TEMPO_DO_NOT_SHOW_IN_NAV_UNTIL_REFRESH, 'true');

      sendNewNavTree = true;
      instantUpdateSuccessful = true;
    });
  } else if (changeItem.type === ChangeType.UNDO) {
    const {
      sendNewNavTree: _sendNewNavTree,
      instantUpdateSuccessful: _instantUpdateSuccessful,
    } = applyUndoChangeItemToDocument(parentPort, changeItem as UndoChange);

    sendNewNavTree = _sendNewNavTree;
    instantUpdateSuccessful = _instantUpdateSuccessful;
  } else if (changeItem.type === ChangeType.REDO) {
    const changeFields = changeItem.changeFields as RedoChangeFields;

    const changeToRedo = changeFields.changeToRedo;

    if (CHANGE_TYPES_WITH_INSTANT_UNDO.includes(changeToRedo.type)) {
      const {
        sendNewNavTree: _sendNewNavTree,
        instantUpdateSuccessful: _instantUpdateSuccessful,
      } = applyChangeItemToDocument(parentPort, storyboardId, changeToRedo);

      sendNewNavTree = _sendNewNavTree;
      instantUpdateSuccessful = _instantUpdateSuccessful;

      if (changeToRedo.prevIdToNewIdMap) {
        updateCodebaseIds(parentPort, changeToRedo.prevIdToNewIdMap, true);
      }
    }
  }

  // Immediately set the new selected element keys to prevent any delay in the outlines updating
  let elementKeyToSelectAfterInstantUpdate =
    changeItem.getElementKeyToSelectAfterInstantUpdate();
  let elementKeysToMultiselectAfterInstantUpdate =
    changeItem.getElementKeysToMultiselectAfterInstantUpdate();

  if (changeItem.type === ChangeType.UNDO) {
    elementKeyToSelectAfterInstantUpdate = (
      changeItem.changeFields as UndoChangeFields
    ).changeToUndo.getElementKeyToSelectAfterUndoInstantUpdate();
    elementKeysToMultiselectAfterInstantUpdate = (
      changeItem.changeFields as UndoChangeFields
    ).changeToUndo.getElementKeysToMultiselectAfterUndoInstantUpdate();
  }

  if (elementKeyToSelectAfterInstantUpdate !== undefined) {
    setMemoryStorageItem(
      SELECTED_ELEMENT_KEY,
      elementKeyToSelectAfterInstantUpdate,
    );
    parentPort.postMessage({
      id: FIXED_IFRAME_MESSAGE_IDS.SELECTED_ELEMENT_KEY,
      elementKey: elementKeyToSelectAfterInstantUpdate,
      outerHTML: $(
        `.${ELEMENT_KEY_PREFIX}${elementKeyToSelectAfterInstantUpdate}`,
      ).get(0)?.outerHTML,
    });
  }

  if (elementKeysToMultiselectAfterInstantUpdate !== undefined) {
    setMemoryStorageItem(
      MULTI_SELECTED_ELEMENT_KEYS,
      elementKeysToMultiselectAfterInstantUpdate,
    );
    parentPort.postMessage({
      id: FIXED_IFRAME_MESSAGE_IDS.MULTI_SELECTED_ELEMENT_KEYS,
      elementKeys: elementKeysToMultiselectAfterInstantUpdate,
      outerHTMLs: elementKeysToMultiselectAfterInstantUpdate?.map(
        (elementKey) =>
          $(`.${ELEMENT_KEY_PREFIX}${elementKey}`).get(0)?.outerHTML,
      ),
    });
  }

  if (instantUpdateSuccessful) {
    // Delete any elements that need to be deleted after instant updates
    $(`*[${TEMPO_DELETE_AFTER_INSTANT_UPDATE}=true]`).remove();
  }

  parentPort.postMessage({
    id: FIXED_IFRAME_MESSAGE_IDS.INSTANT_UPDATE_DONE,
    changeItem: plainChangeItem,
    instantUpdateData: extraInstantUpdateData,
    instantUpdateSuccessful,
  });

  return { sendNewNavTree, instantUpdateSuccessful };
};

const applyUndoChangeItemToDocument = (
  parentPort: any,
  changeItem: UndoChange,
): { sendNewNavTree: boolean; instantUpdateSuccessful: boolean } => {
  const changeFields = changeItem.changeFields;

  const changeToUndo = changeFields.changeToUndo;

  if (!CHANGE_TYPES_WITH_INSTANT_UNDO.includes(changeToUndo.type)) {
    return { sendNewNavTree: false, instantUpdateSuccessful: false };
  }

  let sendNewNavTree = false;
  let instantUpdateSuccessful = false;

  // API has completed and the IDs have been updated, reverse this change
  if (changeToUndo.prevIdToNewIdMap) {
    const undoCodebaseIdChanges: {
      [newCodebaseId: string]: string;
    } = {};
    Object.keys(changeToUndo.prevIdToNewIdMap).forEach((prevId) => {
      const newId = changeToUndo.prevIdToNewIdMap[prevId];
      undoCodebaseIdChanges[newId] = prevId;
    });

    // If undoing do not update the codebase IDs backwards if there are codebase IDs to set after
    // the undo instant update is done
    const selectedElementSpecifiedAfterUndo =
      changeToUndo.getElementKeyToSelectAfterUndoInstantUpdate() !== undefined;
    updateCodebaseIds(
      parentPort,
      undoCodebaseIdChanges,
      !selectedElementSpecifiedAfterUndo,
    );
  }

  // Then undo the actual change
  if (changeToUndo.type === ChangeType.REMOVE_JSX) {
    // Re-add the removed JSX
    const innerChangeFields =
      changeToUndo.changeFields as RemoveJsxChangeFields;
    const codebaseIdsToReadd = innerChangeFields.codebaseIdsToRemove;

    // If it has been flushed, re-create the html elements from the saved inner HTML
    if (changeFields.matchingActivityFlushed) {
      const instantUpdateData = changeToUndo.getInstantUpdateData();
      const parentToElementKeysRemoved: {
        [parentElementKey: string]: any[];
      } = instantUpdateData.parentToElementKeysRemoved || {};

      Object.entries(parentToElementKeysRemoved).forEach(
        ([parentElementKey, itemsRemoved]) => {
          // Sort the removed entries in order of unique path
          const sortedItemsRemoved = Object.values(itemsRemoved).sort(
            (a: any, b: any) => {
              const aElementKey = TempoElement.fromKey(a.elementKeyRemoved);
              const bElementKey = TempoElement.fromKey(b.elementKeyRemoved);
              return aElementKey.uniquePath.localeCompare(
                bElementKey.uniquePath,
              );
            },
          );

          // Find the parent element
          const parentElement = $(
            `.${ELEMENT_KEY_PREFIX}${parentElementKey}`,
          ).get(0);
          if (parentElement) {
            // Add the removed elements back in order
            sortedItemsRemoved.forEach((item: any) => {
              const { elementKeyRemoved, outerHTML } = item;

              const element = TempoElement.fromKey(elementKeyRemoved);
              const indexInParent = Number(element.uniquePath.split('-').pop());

              const newElementFromHtml = $(outerHTML).get(0);

              // Add to the parent in the index
              if (newElementFromHtml) {
                newElementFromHtml.setAttribute(
                  TEMPO_DELETE_AFTER_REFRESH,
                  'true',
                );
                newElementFromHtml.setAttribute(TEMPO_INSTANT_UPDATE, 'true');
                parentElement.insertBefore(
                  newElementFromHtml,
                  parentElement.children[indexInParent] || null,
                );
                instantUpdateSuccessful = true;
                sendNewNavTree = true;
              }
            });
          }
        },
      );
    } else {
      // Not flushed yet so can just re-add
      codebaseIdsToReadd.forEach((codebaseIdToReadd) => {
        $(`.${codebaseIdToReadd}`).each((index, item) => {
          item.classList.remove(TEMPO_DISPLAY_NONE_UNTIL_REFRESH_CLASS);
          item.removeAttribute(TEMPO_DO_NOT_SHOW_IN_NAV_UNTIL_REFRESH);

          sendNewNavTree = true;
          instantUpdateSuccessful = true;
        });
      });
    }
  } else if (
    changeToUndo.type === ChangeType.ADD_CLASS ||
    changeToUndo.type === ChangeType.STYLING
  ) {
    const instantUpdateData = changeToUndo.getInstantUpdateData();
    const innerChangeFields = changeToUndo.changeFields as AddClassChangeFields;

    const addedClass = instantUpdateData?.addedClass;
    if (addedClass) {
      $(`.${innerChangeFields.codebaseIdToAddClass}`).each((index, item) => {
        if ($(item).hasClass(addedClass)) {
          $(item).removeClass(addedClass);
          instantUpdateSuccessful = true;
        }
      });
    }

    const codebaseAddedClass = instantUpdateData?.codebaseAddedClass;
    if (codebaseAddedClass) {
      $(`.${innerChangeFields.codebaseIdToAddClass}`).each((index, item) => {
        if ($(item).hasClass(codebaseAddedClass)) {
          $(item).removeClass(codebaseAddedClass);
          instantUpdateSuccessful = true;
        }
      });
    }
  } else if (changeToUndo.type === ChangeType.ADD_JSX) {
    const instantUpdateData = changeToUndo.getInstantUpdateData();
    const addedIds = instantUpdateData?.addedIds;

    addedIds?.forEach((addedId: string) => {
      $(`.${addedId}`).remove();
      instantUpdateSuccessful = true;
    });

    sendNewNavTree = true;
  }

  return { sendNewNavTree, instantUpdateSuccessful };
};

/**
 * After a change is processed on the backend, we need to update the codebase ids in the document.
 */
export const updateCodebaseIds = (
  parentPort: any,
  prevIdToNewIdMap: {
    [prevCodebaseId: string]: string;
  },
  updateElementKeys?: boolean,
): boolean => {
  // Update codebase ids in the document
  const changes: any = [];
  Object.entries(prevIdToNewIdMap).forEach(
    ([prevCodebaseId, newCodebaseId]) => {
      $(`.${prevCodebaseId}`).each((index: any, item: any) => {
        changes.push({
          item,
          prevCodebaseId,
          newCodebaseId,
        });
      });
    },
  );

  // Codebase Ids can swap, so we have to apply the changes after looking all elements up
  changes.forEach((change: any) => {
    const $item = $(change.item);
    const newClass = ($item.attr('class') || '').replace(
      new RegExp(`${change.prevCodebaseId}`, 'g'),
      change.newCodebaseId,
    );
    $item.attr('class', newClass);

    change.item.setAttribute('tempoelementid', change.newCodebaseId);
    change.item.setAttribute('data-testid', change.newCodebaseId);
  });

  if (!updateElementKeys) {
    return Boolean(changes.length);
  }

  const keysToCheck = [
    {
      key: SELECTED_ELEMENT_KEY,
      messageId: FIXED_IFRAME_MESSAGE_IDS.SELECTED_ELEMENT_KEY,
    },
    {
      key: HOVERED_ELEMENT_KEY,
      messageId: FIXED_IFRAME_MESSAGE_IDS.HOVERED_ELEMENT_KEY,
    },
  ];
  keysToCheck.forEach(({ key, messageId }) => {
    const elementKey = getMemoryStorageItem(key);
    const tempoElement = TempoElement.fromKey(elementKey);
    if (prevIdToNewIdMap[tempoElement.codebaseId]) {
      const newElement = new TempoElement(
        prevIdToNewIdMap[tempoElement.codebaseId],
        tempoElement.storyboardId,
        tempoElement.uniquePath,
      );
      setMemoryStorageItem(key, newElement.getKey());

      parentPort.postMessage({
        id: messageId,
        elementKey: newElement.getKey(),
        outerHTML: $(`.${ELEMENT_KEY_PREFIX}${newElement.getKey()}`).get(0)
          ?.outerHTML,
      });
    }
  });

  // Also update the multiselected element keys
  const multiselectedElementKeys = getMemoryStorageItem(
    MULTI_SELECTED_ELEMENT_KEYS,
  );
  if (multiselectedElementKeys?.length) {
    const newMultiselectedElementKeys: string[] = [];
    multiselectedElementKeys.forEach((elementKey: string) => {
      const tempoElement = TempoElement.fromKey(elementKey);
      if (prevIdToNewIdMap[tempoElement.codebaseId]) {
        const newElement = new TempoElement(
          prevIdToNewIdMap[tempoElement.codebaseId],
          tempoElement.storyboardId,
          tempoElement.uniquePath,
        );
        newMultiselectedElementKeys.push(newElement.getKey());
      } else {
        newMultiselectedElementKeys.push(elementKey);
      }
    });

    setMemoryStorageItem(
      MULTI_SELECTED_ELEMENT_KEYS,
      newMultiselectedElementKeys,
    );
    parentPort.postMessage({
      id: FIXED_IFRAME_MESSAGE_IDS.MULTI_SELECTED_ELEMENT_KEYS,
      elementKeys: newMultiselectedElementKeys,
      outerHTMLs: newMultiselectedElementKeys?.map(
        (elementKey) =>
          $(`.${ELEMENT_KEY_PREFIX}${elementKey}`).get(0)?.outerHTML,
      ),
    });
  }

  return Boolean(changes.length);
};
