import { initChannelMessaging } from './channelMessaging';

export const TempoDevtools = {
  state: {
    dependencies: {
      LzString: null,
    },
    env: {},
  },

  // Initialization method
  init: function (customEnv = {}) {
    if (customEnv) {
      this.state.env = {
        ...customEnv,
      };
    }

    initChannelMessaging();
  },
};
