import {
  ADD_JSX_PREFIX,
  DUPLICATE_PLACEHOLDER_PREFIX,
  TEMPORARY_STYLING_CLASS_NAME,
  WRAP_IN_DIV_PLACEHOLDER_CODEBASE_ID,
} from './changeItemFunctions';

// Must match identifier utils on the frontend
export const ELEMENT_KEY_PREFIX = 'arb89-key-';
export const UNIQUE_LOOKUP_PREFIX = 'arb89-lookup-'; // Used only for processing the nav tree
export const OUTLINE_CLASS = 'arb89-outline';
export const EDIT_TEXT_BUTTON = 'arb89-edit-text-button';
export const TEMPO_INSTANT_DIV_DRAW_CLASS = 'arb89-instant-div-draw';
export const TEMPO_MOVE_BETWEEN_PARENTS_OUTLINE =
  'arb89-move-between-parents-outline';
export const TEMPO_INSTANT_UPDATE_STYLING_PREFIX = 'arb89-styling-';
export const TEMPO_DISPLAY_NONE_UNTIL_REFRESH_CLASS =
  'arb89-display-none-until-refresh';
export const TEMPO_DELETE_AFTER_INSTANT_UPDATE =
  'arb89-delete-after-instant-update';

const KNOWN_CLASSES = new Set([
  OUTLINE_CLASS,
  TEMPO_INSTANT_DIV_DRAW_CLASS,
  TEMPO_MOVE_BETWEEN_PARENTS_OUTLINE,
  TEMPO_DISPLAY_NONE_UNTIL_REFRESH_CLASS,
  WRAP_IN_DIV_PLACEHOLDER_CODEBASE_ID,
  TEMPORARY_STYLING_CLASS_NAME,
  EDIT_TEXT_BUTTON,
]);
const KNOWN_CLASS_PREFIXES = [
  ELEMENT_KEY_PREFIX,
  UNIQUE_LOOKUP_PREFIX,
  TEMPO_INSTANT_UPDATE_STYLING_PREFIX,
  DUPLICATE_PLACEHOLDER_PREFIX,
  ADD_JSX_PREFIX,
];

// Attributes that are set until the next Nav Tree Refresh
export const TEMPO_INSTANT_UPDATE = 'arb89-instant-update';
export const TEMPO_DELETE_AFTER_REFRESH = 'arb89-delete-after-refresh';
export const TEMPO_OUTLINE_UNTIL_REFESH = 'arb89-outline-until-refresh';
export const TEMPO_DO_NOT_SHOW_IN_NAV_UNTIL_REFRESH =
  'arb89-do-not-show-in-nav';
export const TEMPO_ELEMENT_ID = 'tempoelementid';
export const TEMPO_TEST_ID = 'data-testid';
export const TEMPO_QUEUE_DELETE_AFTER_HOT_RELOAD =
  'arb89-queue-delete-after-hot-reload';

export const KNOWN_ATTRIBUTES = new Set([
  TEMPO_INSTANT_UPDATE,
  TEMPO_DELETE_AFTER_REFRESH,
  TEMPO_DELETE_AFTER_INSTANT_UPDATE,
  TEMPO_OUTLINE_UNTIL_REFESH,
  TEMPO_QUEUE_DELETE_AFTER_HOT_RELOAD,
  TEMPO_DO_NOT_SHOW_IN_NAV_UNTIL_REFRESH,
  TEMPO_ELEMENT_ID,
  TEMPO_TEST_ID,
]);

export const validateUuid = (uuid: string) => {
  return new RegExp(
    '^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$',
    'i',
  ).test(uuid);
};

export const getCodebaseIdFromClassName = (
  className: string,
): string | null => {
  if (
    className &&
    className.startsWith('tempo-') &&
    (validateUuid(className.substring('tempo-'.length)) ||
      className === WRAP_IN_DIV_PLACEHOLDER_CODEBASE_ID ||
      className.startsWith(DUPLICATE_PLACEHOLDER_PREFIX))
  ) {
    return className;
  }

  return null;
};

export const getCodebaseIdFromNode = (node: any): string | null => {
  if (!node?.classList) {
    return null;
  }

  let clsFound: any = null;
  node.classList.forEach((cls: string) => {
    // A bit of a hack -> in the case when there are multiple tempo codebase IDs on an element
    // we want to use the first one defined. This happens in the case of forward refs, and we
    // always add the bottom-most className to the end of the classes list, so we want to
    // show the top-most element in this case
    if (clsFound) {
      return;
    }

    if (!cls) {
      return;
    }

    const extractedId = getCodebaseIdFromClassName(cls);
    if (extractedId) {
      clsFound = extractedId;
    }
  });

  if (clsFound) {
    return clsFound;
  }

  if (node?.tagName?.toLowerCase() == 'body') {
    return 'body';
  }

  if (node?.id == 'root') {
    return 'root';
  }

  if (node?.id == '__next') {
    return '__next';
  }

  return null;
};

// Track how many times we've tried to add an element key class to nodes that keep getting reset
// Some DOM elements are controlled by external libraries/frameworks (e.g. ProseMirror) and will reset any classes or
// wipe out the DOM elements and re-render them.
// We don't want to keep trying indefinitely to add classes to these elements since it the reset will trigger DOM updates
// which again trigger the mutation observer and we'll start the whole process over again.
const resetCounts = new Map<string, number>();
const RESET_COUNT_THRESHOLD = 5; // Stop trying to add classes after 5 failed attempts

export const addElementKeyAsClass = ({
  node,
  safeElementKey,
  codebaseId,
}: {
  node: any;
  safeElementKey: string;
  codebaseId?: string;
}) => {
  if (!node?.classList) {
    return;
  }

  let successfullySetBefore = false;

  const classesToRemove = new Set<string>();
  node.classList.forEach((cls: string) => {
    if (cls?.startsWith(ELEMENT_KEY_PREFIX)) {
      successfullySetBefore = true;

      classesToRemove.add(cls);
    }
  });

  // If this element:
  // 1. Doesn't have a codebase ID (meaning it's not one of our components)
  // 2. Has never successfully kept our class before
  // 3. Has hit the reset threshold
  // Then stop trying to add classes to it since it's likely controlled externally
  if (
    !codebaseId &&
    !successfullySetBefore &&
    (resetCounts.get(safeElementKey) || 0) >= RESET_COUNT_THRESHOLD
  ) {
    return;
  }

  classesToRemove.forEach((cls: string) => {
    node.classList.remove(cls);
  });

  node.classList.add(`${ELEMENT_KEY_PREFIX}${safeElementKey}`);

  // For elements without a codebase ID that we haven't successfully added a class to before,
  // check after a delay if our class was removed by external code
  // If so, increment the reset count to eventually stop trying
  if (!codebaseId && !successfullySetBefore) {
    setTimeout(() => {
      const element = document.querySelector(
        `${ELEMENT_KEY_PREFIX}${safeElementKey}`,
      );
      if (!element) {
        resetCounts.set(
          safeElementKey,
          (resetCounts.get(safeElementKey) || 0) + 1,
        );
      }
    }, 1000);
  }
};

export const addUniqueLookupAsClass = (node: any, uniqueLookup: string) => {
  if (!node?.classList) {
    return;
  }

  const classesToRemove = new Set<string>();
  node.classList.forEach((cls: string) => {
    if (cls?.startsWith(UNIQUE_LOOKUP_PREFIX)) {
      classesToRemove.add(cls);
    }
  });

  classesToRemove.forEach((cls: string) => {
    node.classList.remove(cls);
  });

  node.classList.add(`${UNIQUE_LOOKUP_PREFIX}${uniqueLookup}`);
};

export const addCodebaseIdToNode = (node: any, codebaseId: string) => {
  if (!node?.classList) {
    return;
  }

  const classesToRemove = new Set<string>();
  node.classList.forEach((cls: string) => {
    if (!cls) {
      return;
    }

    if (getCodebaseIdFromClassName(cls)) {
      classesToRemove.add(cls);
    }
  });

  classesToRemove.forEach((cls: string) => {
    node.classList.remove(cls);
  });

  node.classList.add(codebaseId);
};

export const getElementKeyFromNode = (node: any): string | null => {
  if (!node?.classList) {
    return null;
  }

  let clsFound = null;
  node.classList.forEach((cls: string) => {
    if (cls?.startsWith(ELEMENT_KEY_PREFIX)) {
      clsFound = cls.substring(ELEMENT_KEY_PREFIX.length);
    }
  });
  return clsFound;
};

export const getUniqueLookupFromNode = (node: any): string | null => {
  if (!node?.classList) {
    return null;
  }

  let clsFound = null;
  node.classList.forEach((cls: string) => {
    if (cls?.startsWith(UNIQUE_LOOKUP_PREFIX)) {
      clsFound = cls.substring(UNIQUE_LOOKUP_PREFIX.length);
    }
  });
  return clsFound;
};

export const removeUniqueLookupFromNode = (node: any): boolean => {
  if (!node?.classList) {
    return false;
  }

  const classesToRemove = new Set<string>();
  node.classList.forEach((cls: string) => {
    if (cls?.startsWith(UNIQUE_LOOKUP_PREFIX)) {
      classesToRemove.add(cls);
    }
  });

  classesToRemove.forEach((cls: string) => {
    node.classList.remove(cls);
  });

  return classesToRemove.size > 0;
};

export const hasClass = (node: any, klass: string): boolean => {
  if (!node?.classList) {
    return false;
  }

  let hasClass = false;
  node.classList.forEach((cls: string) => {
    if (cls == klass) {
      hasClass = true;
    }
  });
  return hasClass;
};

export const isOutline = (node: any): boolean => {
  return hasClass(node, OUTLINE_CLASS);
};

export const getAllUnknownClasesFromList = (classes: string[]): string[] => {
  return classes.filter((cls) => {
    if (!cls) {
      return false;
    }

    const isCodebaseId = getCodebaseIdFromClassName(cls) !== null;

    const clsStartsWithKnownPrefix = KNOWN_CLASS_PREFIXES.some((prefix) =>
      cls.startsWith(prefix),
    );

    if (!clsStartsWithKnownPrefix && !KNOWN_CLASSES.has(cls) && !isCodebaseId) {
      return true;
    }

    return false;
  });
};

export const getAllUnknownClasses = (node: any): string[] => {
  if (!node?.classList) {
    return [];
  }

  return getAllUnknownClasesFromList(Array.from(node.classList));
};

/**
 * Accepts a node from mutation observer and determines if it's a moving element.
 * Returns moving if and only if the node has the attribute `arb89-instant-update` set to true.
 * @param node
 * @returns
 */
export const isMovingElement = (node: any): boolean => {
  if (!node) {
    return false;
  }

  // Check if the node responds to getAttribute method.
  if (typeof node.getAttribute !== 'function') {
    return false;
  }

  return node.getAttribute(TEMPO_INSTANT_UPDATE) === 'true';
};

export const isSkipNavTreeNode = (node: any) => {
  if (!node) {
    return;
  }

  return node.getAttribute(TEMPO_DO_NOT_SHOW_IN_NAV_UNTIL_REFRESH) === 'true';
};

/**
 * Check if the node has any parent that is an svg tag
 */
export const isElementInSvg = (node: any, parent?: boolean): boolean => {
  if (!node) {
    return false;
  }

  if (parent && node.tagName?.toLowerCase() === 'svg') {
    return true;
  }

  if (node.parentNode) {
    return isElementInSvg(node.parentNode, true);
  }

  return false;
};
